<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';

const conditions = [
  {
    label: 'A',
    info: 'Вы гражданин РФ;',
  },
  {
    label: 'Б',
    info: 'Вы официально трудоустроены или пенсионер;',
  },
  {
