  },
  {
    label: 'Г',
    info: 'Ответ от банка придет в течение минуты.',
  },
];

export default {
  name: 'CreditConditions',
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiperOptions: {
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
    };
  },
  computed: {
    conditions() {
      return conditions;
    },
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
    lastCondition() {
      return conditions[conditions.length - 1];
    },
  },
};
