<template>
  <section class="cancel-page">
    <form-wrapper>
      <template v-slot:title>
        В чем причина вашего отказа?
      </template>

      <template v-slot:default>
        <radio-list
          v-slot="{ item }"
          :selected-value="pickedType"
          :items="items"
          name="cancellation-reason"
          @input="onInput"
        >
          <textarea
            v-if="item.textarea"
            ref="textarea"
            v-model="answer"
            class="pos-textarea"
            placeholder="Например: пятница 13е"
          />
          <template v-else>
            {{ item.title }}
          </template>
        </radio-list>
      </template>

      <template v-slot:buttons>
        <inline-buttons>
          <template v-slot:main>
            <pos-button
              kind="outline"
              @click="onButtonCancelClick"
            >
              Отменить заявку
            </pos-button>
          </template>
          <template v-slot:secondary>
            <div @click="$router.go(-1)">
              Назад
            </div>
          </template>
        </inline-buttons>
      </template>
    </form-wrapper>
  </section>
</template>

<script>
import FormWrapper from '@/components/FormWrapper';
import RadioList from '@/components/RadioList';
import PosButton from '@/components/Button';
import InlineButtons from '@/components/InlineButtons';

const items = [
  {
    id: '1',
    value: 'just-look',
    title: 'Просто посмотрел',
  },
  {
    id: '2',
    value: 'no-credit',
    title: 'Передумал брать кредит',
  },
  {
    id: '3',
    value: 'goods',
    title: 'Хочу другой товар',
  },
  {
    id: '4',
    value: 'conditions',
    title: 'Не устроили условия',
  },
  {
    id: '5',
    value: 'need-info',
    title: 'Не хватило информации',
  },
  {
    id: '6',
    value: 'answer',
    title: '',
    textarea: true,
  },
];

export default {
  name: 'CancelPage',
  components: {
    FormWrapper,
    RadioList,
    PosButton,
    InlineButtons,
  },
  data() {
    return {
      pickedType: 'goods',
      items,
      answer: '',
    };
  },
  methods: {
    onInput(val) {
      if (val === 'answer') {
        this.$nextTick(() => {
          this.$refs.textarea.focus();
        });
      } else {
        this.answer = '';
      }
      this.pickedType = val;
    },
    onButtonCancelClick() {
      this.$router.push('/');
    },
  },
};
</script>

<style lang="scss">
  .cancel-page {
    .form-wrapper {
      &__title {
        margin-bottom: 40px;
      }
    }
  }

  .pos-textarea {
    display: inline-block;
    width: 100%;
    height: 64px;
    padding: 8px 24px;
    border: 0;
    border-radius: 8px;
    color: $gray_1;
    background: $gray_8;
    resize: none;

    @include media-breakpoint-up(lg) {
      width: 374px;
    }
  }
</style>
