<template>
  <div
    v-if="isShowModal"
    class="step-modal"
    @click="showStepperModal = false"
  >
    <div class="step-modal__inner">
      <div class="step-modal__icon">
        <box />
      </div>
      <div class="step-modal__text">
        Заявка отправлена на <br> предварительную проверку
      </div>
    </div>
    <img
      class="step-modal__close"
      src="@/assets/close.svg"
      alt="close-icon"
      width="14"
      height="14"
      @click="$emit('close')"
    >
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';
import { ROUTE_IDS } from '@/utils/constants';

import { Box } from '@/components/icons';

export default {
  name: 'StepperModal',
  components: { Box },
  computed: {
    ...mapFields(['modals.showStepperModal']),
    currentRouteId() {
      return this.$route.meta.id;
    },
    isShowModal() {
      return (this.currentRouteId > ROUTE_IDS.CONTACT_INFO) && this.showStepperModal;
    },
  },
};
</script>

