const additionalServices = () => ({
  lifeInsurance: false,
  workInsurance: false,
  multiDefence: false,
  multiService: false,
  smsService: true,
});

export default new Vuex.Store({
  state: {
    accessID: 3399,
    tradeID: 12155,
    orderID: '',
    phone: '',
    formType: 'short',
    form: defaultClientData(),
    firstPayment: 0,
    totalSum: 200000,
    step: 1,
    defaultProducts: [],
    product: null,
    additionalServices: additionalServices(),
    selectedServices: [],
    status: '',
    session: '',
    modals: {
      showCredits: true,
      showStepperModal: true,
    },
    isLoading: false,
    manualAddress: false,
    showDocument: false,

    // используется для проверка на полный адрес
    completeAddress: false,

    // используется для тестирования сценариев с отображением конкретных продуктов для партнера
    displayProducts: '',
  },
  getters: {
    /**
     * Посчитать сумму всех выбранных доп. услуг
     * @param state
     * @returns {number}
     */
    servicesSum: state => state.selectedServices
      .reduce((sum, service) => sum + service.price, 0),
    /* Фамилия Имя Отчество */
    FIO(state) {
      const [lastName, firstName, middleName] = state.form.clientName.trim().split(' ');
      return { middleName, firstName, lastName };
    },

    getField,
  },
  mutations: {
    /**
     * Запустить мутацию, когда нужно установить инит значения в state.form
     * @param state
     * @param data
     */
    setInitFormValues(state, data = {}) {
      Object.assign(state.form, data);
    },
    /**
     * Обновить несколько полей в state.form
     * Пример: { clientEmail: 'test@test.ru', clientName: 'Ивано Иван' }
     * @param state
     * @param data
     */
    updateFormValues(state, data) {
      Object.keys(data).forEach((key) => {
        state.form[key] = data[key];
      });
    },
    /**
     * Обновить поле в state.form
     * Пример: { name: clientEmail, value: 'test@test.ru' }
     * @param state
     * @param name
     * @param value
     */
    updateFormField(state, { name, value }) {
      state.form[name] = value;
    },
    setState(state, { name, value }) {
      state[name] = value;
    },
    /**
     * Добавить доп. услугу
     * Пример: { name: workInsurance, value: true }
     * @param state
     * @param name
     * @param value
     */
    setService(state, { name, value }) {
      state.additionalServices[name] = value;
    },
    /**
     * Добавить или удалить выбранную доп. услугу
     * @param state
     * @param {boolean} checked
     * @param {Object} service
     */
    setServices(state, { checked, service }) {
      if (checked) {
        state.selectedServices.push(service);
      } else {
        const index = state.selectedServices
          .findIndex(item => item.name === service.name);
        state.selectedServices.splice(index, 1);
      }
    },
    setFullMobilePhone(state, { name, value }) {
      const countryCode = '+7 ';
      state.form[name] = countryCode + value;
    },
    updateField,
  },
  actions: {
    setService({ commit }, payload = {}) {
      const { checked, service } = payload;
      commit('setService', { name: service.name, value: checked });
      commit('setServices', { checked, service });
    },
    removeService({ commit }, serviceName = '') {
      commit('setServices', { checked: false, service: { name: serviceName } });
      commit('setService', { name: serviceName, value: false });
    },
    async startPosCreditSession({ commit, state }) {
      try {
        const { accessID, tradeID, orderID } = state;
        const postData = {
          accessID,
          tradeID,
          orderID,
          phone: '',
        };
        const { data } = await posApi.startSession(postData);
        commit('setState', {
          name: 'session',
          value: data.session,
        });
      } catch (e) {
        console.log(e);
      }
    },

    async fetchPosCreditProducts({ commit, state }) {
      try {
        const { accessID, tradeID } = state;
        const { data } = await posApi.fetchProducts({ accessID, tradeID });
        commit('setState', { name: 'defaultProducts', value: data.stock });
      } catch (e) {
        console.log(e);
      }
    },

    async sendShortLoan({ commit, state, getters }) {
      try {
        const {
          accessID,
          tradeID,
          orderID,
          hostname,
          session,
          items,
          firstPayment,
        } = state;

        const {
          firstName,
          lastName,
          middleName,
        } = getters.FIO;

        const request = {
          accessID,
          hostname,
          session,
          orderID,
          comment: addComment(state),
          firstName,
          lastName,
          surName: middleName,
          email: state.form.clientEmail,
          firstPayment,
          goodParams: items,
          mobilePhone: state.form.clientMobilePhone,
          period: state.product.duration,
          tradeID,
          type: state.products.creditType,
        };

        const { data } = await posApi.sendRequest(request);
        commit('setState', {
          name: 'status',
          value: data,
        });
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      }
    },
  },
  plugins: [
    createPersistedState({
      key: 'online_form_vuex',
      paths: [
        'accessID',
        'tradeID',
        'displayProducts',
        'form',
        'formType',
        'firstPayment',
        'totalSum',
        'product',
        'additionalServices',
        'selectedServices',
        'modals',
        'completeAddress',
        'items',
      ],
    }),
  ],
});
