<template>
  <div class="app">
    <div class="main">
      <div class="container">
        <pos-header />
        <page-wrapper>
          <router-view />
        </page-wrapper>
      </div>
    </div>
    <pos-footer />
    <portal-target name="modal" />
  </div>
</template>

<script>
import _get from 'lodash.get';
import { fieldDeps } from '@/utils/constants';
import { requestParams } from '@/utils/requestParams';

import PosHeader from '@/components/Header';
import PageWrapper from '@/components/PageWrapper';
import PosFooter from '@/components/Footer';
import { mapActions } from 'vuex';

export default {
  name: 'App',
  components: {
    PosHeader,
    PageWrapper,
    PosFooter,
  },
  mounted() {
    requestParams();
    this.startPosCreditSession();
    this.fetchPosCreditProducts();

    this.unwatch = this.$watch(
      () => (
        fieldDeps['form.scoring'].map(field => _get(this.$store.state, field))
      ).join(),
      () => {
        this.$store.commit('updateFormField', { name: 'scoring', value: true });
      },
    );
  },
  beforeDestroy() {
    this.unwatch();
  },
  methods: {
    ...mapActions(['fetchPosCreditProducts', 'startPosCreditSession']),
  },
};
</script>

<style lang="scss">
  @import 'assets/styles/main';

  .app {
    padding: 16px 0 11px;
    -webkit-font-smoothing: antialiased;
