<template>
  <div class="total-sum">
    <div class="total-sum__title">
      Сумма заказа
    </div>
    <div class="total-sum__price">
      {{ sum }} ₽
    </div>
  </div>
</template>

<script>
import { numberToCurrency } from '@/utils/number';

export default {
  name: 'TotalSum',
  computed: {
    totalSum() {
      return this.$store.state.totalSum;
    },
    sum() {
      return numberToCurrency(this.totalSum);
    },
  },
};
</script>

