<template>
  <div
    class="pos-services"
    :class="{ '_show-gradient': $mq === 'desktop' && showGradient }"
  >
    <div class="pos-services__header">
      <div
        class="pos-services__title"
        @click="show = !show"
      >
        <div class="pos-services__header-icon">
          <component :is="show ? 'ChevronBottom' : 'ChevronTop'" />
        </div>
        <div>
          Услуги, которые могут быть вам полезны
          <span class="pos-services__selected">
            выбрано {{ selectedServices.length }} {{ pluralised }}
          </span>
        </div>
      </div>
    </div>
    <component
      :is="$mq === 'desktop' ? 'swiper' : 'div'"
      v-show="show"
      ref="mySwiper"
      :options="swiperOptions"
      class="pos-services__services"
      @reach-end="onReachEnd"
      @from-edge="onFromEdge"
    >
      <component
        :is="$mq === 'desktop' ? 'swiper-slide' : 'div'"
        v-for="service in services"
        :key="service.name"
      >
        <service
          :service="service"
          @change="checked => select(checked, service)"
        />
      </component>
    </component>
  </div>
</template>

<script>
import { getPlural } from '@/utils/string';

import { Swiper, SwiperSlide } from 'vue-awesome-swiper';

import ChevronBottom from '@/components/icons/ChevronBottom';
import ChevronTop from '@/components/icons/ChevronTop';
import Service from './Service';

/* https://swiperjs.com/api/ */
const swiperOptions = {
  slidesPerView: 'auto',
  spaceBetween: 8,
  grabCursor: true,
  mousewheel: true,
};

export default {
  name: 'Services',
  components: {
    ChevronBottom,
    ChevronTop,
    Service,
    Swiper,
    SwiperSlide,
  },
  props: {
    services: {
      type: Array,
      required: true,
    },
    selectedServices: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      show: true,
      swiperOptions,
      showGradient: true,
    };
  },
  computed: {
    pluralised() {
      return getPlural(this.selectedServices.length, ['услуга', 'услуги', 'услуг']);
    },
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  methods: {
    select(checked, service) {
      this.$store.dispatch('setService', { checked, service });
    },
    onReachEnd() {
      this.showGradient = false;
    },
    onFromEdge() {
      this.showGradient = true;
    },
  },
};
</script>

