    }

    &__selected {
      margin-left: 16px;
      font-size: 12px;
      font-weight: 500;
      line-height: 24px;
      color: $gray_2_darker;
    }

    &__services {
      display: flex;
      flex-wrap: nowrap;
      margin-right: -16px;
      overflow-x: auto;

      > div {
        flex-grow: 1;
        flex-shrink: 0;
        margin-right: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    @include media-breakpoint-up(lg) {
      position: relative;
      margin-right: -24px;

      &__services {
        display: block;
        overflow-x: hidden;
        margin: 0;

        > div {
          margin: 0;
        }
      }

      &._show-gradient {
        &::after {
          content: '';
          display: block;
          position: absolute;
          z-index: 1;
          top: 0;
          right: 0;
          width: 34px;
