<template>
  <div
    class="pos-product-item"
    :class="{ _selected: selected }"
    @click="$emit('select')"
  >
    <div class="pos-product-item__type">
      {{ product.title }}
    </div>
    <div class="pos-product-item__price">
      {{ value }} <span>₽</span>
    </div>
    <div class="pos-product-item__duration">
      {{ product.durationStr }}
    </div>
  </div>
</template>

<script>
import { numberToCurrency, roundTo10 } from '@/utils/number';

export default {
  name: 'Product',
  props: {
    product: {
      type: Object,
      required: true,
    },
    selected: Boolean,
  },
  computed: {
    value() {
      return numberToCurrency(roundTo10(this.product.value));
    },
  },
};
</script>

