import { CalculatorUtils } from '@/utils/calculator-utils';
import { INSURANCE_TARIFFS, CREDIT_DURATIONS } from '@/utils/constants';

/**
 * Посчитать сколько нужно платить в месяц
 * для коробочных продуктов
 * @param {number} totalSum - общая сумма
 * @param {number} months - количество месяцев
 * @returns {number}
 */
export const sumPerMonth = (totalSum, months) => {
  const RATE = months === CREDIT_DURATIONS.TWELVE_MONTHS ? 24 : 20;
  const ONE_YEAR_IN_MONTHS = 12;

  const annuityRatio = CalculatorUtils.getAnnuityRatio({
    ratePerMonthInPercents: RATE / ONE_YEAR_IN_MONTHS,
    termMonth: months,
  });

  return parseInt(CalculatorUtils.getPreQualifiedMonthPayment({
    sumLoan: totalSum,
    annuityRatio,
  }),
  10);
};

/**
 * Посчитать сколько нужно платить в месяц без сложных формул
 * @param sum - общая сумма
 * @param months - количество месяцев
 * @returns {number}
 */
export const sumPerMonthsSimple = (sum, months) => Math.ceil(sum / months);

/**
 * Посчитать сумму страховки
 * @param sum - сумма кредита
 * @param months - срок кредита в месяцах
 * @param insuranceType - тип страховки
 * @returns {number}
 */
export const insuranceCalculator = (sum, months, insuranceType) => {
  const TARIFF = INSURANCE_TARIFFS[insuranceType];
  return Math.ceil((sum * TARIFF * months) / (1 - (TARIFF * months)));
};
