import { http } from '@/utils/http';

const BASE_URL = 'https://api.b2otp.ru/shop/';

const START_SESSION = 'action.php?name=loadSession';
const FETCH_PRODUCTS = 'action.php?name=loadStock';
const SEND_REQUEST = 'action.php?name=sendShortLoan';

const headers = {
  'Content-type': 'application/x-www-form-urlencoded',
};

const fetchProducts = data => http({
  method: 'post',
  url: FETCH_PRODUCTS,
  baseURL: BASE_URL,
  data,
  headers,
});

const startSession = data => http({
  method: 'post',
  url: START_SESSION,
  baseURL: BASE_URL,
  data,
  headers,
});

const sendRequest = data => http({
  method: 'post',
  url: SEND_REQUEST,
  baseURL: BASE_URL,
  data,
  headers,
});


export default {
  fetchProducts,
  sendRequest,
  startSession,
};
