<template>
  <div class="online-tabs">
    <div
      v-for="(tab, index) in tabs"
      :key="index"
      class="online-tabs__tab"
      :class="{_selected: isSelected(tab.value)}"
      @click="onTabSelect(tab.value)"
    >
      {{ tab.title }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'Tabs',
  props: {
    tabs: {
      type: Array,
      required: true,
    },
    value: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      currentTab: this.value,
    };
  },
  watch: {
    value: {
      handler(v) {
        this.currentTab = v;
      },
      immediate: true,
    },
  },
  methods: {
    isSelected(value) {
      return value === this.currentTab;
    },
    onTabSelect(value) {
      if (this.currentTab === value) {
        return;
      }
      this.currentTab = value;
      this.$emit('input', value);
    },
  },
};
</script>

<style lang="scss">
  .online-tabs {
    display: inline-flex;
    max-width: 80%;

    &__caption {
      padding-right: 15px;

      @include media-breakpoint-up(lg) {
        padding-right: 35px;
      }
    }


    &__tab {
      position: relative;
      height: $switcher-btn-height;
      font-size: 16px;
      text-align: center;
      width: 124px;
      margin: 0 5px;
      padding: 6px 8px;
      border-color: transparent;
      color: $black_1;
      transition: all .1s ease 0s;
      cursor: pointer;

      &._selected {
        background: $dark;
        color: $white;
        border-radius: 35px;
      }
    }
  }
</style>
