/*
 Сервис получения подсказок Dadata
 https://dadata.ru/api/suggest/
*/

import { post } from '@/utils/http';

const API = process.env.VUE_APP_DADATA_API;
const TOKEN = process.env.VUE_APP_DADATA_KEY;

const concatUrl = query => `${API}${query}`;

/**
 * Возвращает варианты подсказок
 * @param {string} type - тип подсказки (раздел "Возможности")
 * @param {Object} data - параметры запроса
 * @returns {Promise<*>}
 */
const getSuggestions = (type, data) => {
  const url = concatUrl(type);
  const headers = {
    'Content-Type': 'application/json',
    Accept: 'application/json',
    Authorization: `Token ${TOKEN}`,
  };
  const params = {
    headers,
  };
  return post(
    url,
    data,
    params,
  )
    .then(result => result);
};

export default getSuggestions;

