import axios from 'axios';

const BASE_CONFIG = {
  timeout: 30000,
};

export const http = axios.create(BASE_CONFIG);

const onSuccessHandler = response => response.data;

const onErrorHandler = error => error.response;

export const get = async (url, params = {}, headers = {}) => http.get(
  url,
  {
    params,
    headers,
  },
)
  .then(onSuccessHandler)
  .catch(onErrorHandler);

export const post = async (url, data = {}, params = {}) => http.post(
  url,
  data,
  params,
)
  .then(onSuccessHandler)
  .catch(onErrorHandler);
