<template>
  <div class="personal-data">
    <portal
      v-if="showDocument"
      to="modal"
    >
      <modal-window
        @close="closeModal"
      >
        <component :is="document" />
      </modal-window>
    </portal>
    <form-wrapper>
      <template v-slot:info>
        Пожалуйста, представьтесь. Чем больше вы расскажете о себе, тем выше шансы на <br>
        получение кредита. После отправки заявки вам позвонит сотрудник Банка.
      </template>

      <template v-slot:title>
        Персональные данные
      </template>

      <template v-slot:default>
        <form-item
          :with-slotted-error="$v.clientName.$error"
          :error="$$error.fioText(clientName)"
          description="Укажите точно как в паспорте"
        >
          <suggestions
            v-model="clientName"
            title="Фамилия Имя Отчество"
            placeholder="Петров Иван Валентинович"
            translate
            :mask="$$mask.fioMask"
            :error="$v.clientName.$error"
            :valid="!$v.clientName.$invalid"
            type="fio"
            @blur="onFIOBlur"
            @select="setGender"
          />
        </form-item>

        <form-item
          v-if="showGender"
          :with-slotted-error="$v.sex.$error"
          error="Укажите ваш пол"
        >
          <div class="online-tabs__container">
            <span class="online-tabs__caption">
              Пол:
            </span>
            <tabs
              v-model="sex"
              :tabs="tabs"
            />
          </div>
        </form-item>

        <div class="row">
          <div class="col-lg-5 col-md-12">
            <form-item
              :with-slotted-error="$v.dateOfBirth.$error"
              :error="$$error.dateOfBirthText($v.dateOfBirth.isValidDate, dateOfBirth)"
            >
              <pos-input
                v-model="dateOfBirth"
                placeholder="12.12.1999"
                :pattern="$$pattern.datePattern"
                inputmode="decimal"
                :mask="$$mask.date"
                :error="$v.dateOfBirth.$error"
                :valid="!$v.dateOfBirth.$invalid"
                @blur="$v.dateOfBirth.$touch()"
              >
                Дата рождения
              </pos-input>
            </form-item>
          </div>
          <div class="col-lg-7 col-md-12">
            <form-item
              :with-slotted-error="$v.passportInfo.$error"
              error="Укажите серию и номер паспорта"
            >
              <pos-input
                v-model="passportInfo"
                inputmode="decimal"
                placeholder="1234-123456"
                pattern="____-______"
                :mask="$$mask.passportInfo"
                :error="$v.passportInfo.$error"
                :valid="!$v.passportInfo.$invalid"
                @blur="$v.passportInfo.$touch()"
              >
                Серия и номер паспорта
              </pos-input>
            </form-item>
          </div>
        </div>

        <form-item
          :with-slotted-error="$v.fmsDate.$error"
          :error="$$error.fmsDateText($v.fmsDate.isValidDate, fmsDate)"
        >
          <pos-input
            v-model="fmsDate"
            placeholder="01.01.1990"
            :pattern="$$pattern.datePattern"
            inputmode="decimal"
            :mask="$$mask.date"
            :error="$v.fmsDate.$error"
            :valid="!$v.fmsDate.$invalid"
            @blur="$v.fmsDate.$touch()"
          >
            Дата выдачи паспорта
          </pos-input>
        </form-item>
        <div
          class="personal-data__inline-section"
          :class="{ 'd-flex': $mq === 'desktop' }"
        >
          <form-item
            :with-slotted-error="$v.monthIncome.$error"
            error="Укажите ваш персональный доход"
          >
            <pos-input
              v-model="monthIncome"
              maxlength="9"
              inputmode="decimal"
              :mask="$$mask.currencyMask"
              :error="$v.monthIncome.$error"
              @blur="$v.monthIncome.$touch()"
            >
              <template v-slot:maskIcon>
                ₽
              </template>
              Персональный доход в месяц
            </pos-input>
          </form-item>
          <form-item>
            <pos-checkbox
              v-model="pension"
              bold-font
            >
              Я получаю пенсию
            </pos-checkbox>
          </form-item>
        </div>

        <form-item>
          <div class="personal-data__agreement">
            <pos-checkbox
              v-model="$v.agree.$model"
              :invalid="$v.agree.$error"
            >
              <span class="personal-data__agreement-text">
                Я принимаю
                <div
                  class="personal-data__link-text"
                >
                  <span
                    @click="showModal('Conditions')"
                  >
                    условия обработки персональных данных
                  </span>
                </div>
                и
                <div class="personal-data__link-text">
                  <span
                    @click="showModal('CreditParameters')"
                  >
                    ознакомлен с параметрами кредитования.
                  </span>
                </div>
              </span>
            </pos-checkbox>
          </div>
        </form-item>
      </template>

      <template v-slot:buttons>
        <pos-button
          :disabled="$v.$error"
          @click="click"
        >
          К выбору продукта
        </pos-button>
      </template>
    </form-wrapper>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';
import { mapGetters } from 'vuex';
import { personalData } from '@/utils/validation';
import { SOCIAL_STATUSES } from '@/utils/constants';
import { getFirstLetter } from '@/utils/string';

import FormWrapper from '@/components/FormWrapper';
import PosButton from '@/components/Button';
import PosCheckbox from '@/components/Checkbox';
import PosInput from '@/components/Input';
import FormItem from '@/components/FormItem';
import Suggestions from '@/components/Suggestions';
import Tabs from '@/components/Tabs';
import Conditions from '@/documents/ProcessingConditions';
import ModalWindow from '@/components/ModalWindow';
import CreditParameters from '@/documents/CreditParameters';

const GENDER_NOT_FOUND = 'UNKNOWN';

export default {
  name: 'PersonalData',
  components: {
    CreditParameters,
    ModalWindow,
    PosInput,
    PosButton,
    FormWrapper,
    PosCheckbox,
    FormItem,
    Suggestions,
    Tabs,
    Conditions,
  },
  data() {
    return {
      agree: false,
      showGender: false,
      tabs: [
        { value: 'm', title: 'Мужской' },
        { value: 'f', title: 'Женский' },
      ],
      document: '',
    };
  },
  validations: personalData,
  computed: {
    ...mapFields({
      clientName: 'form.clientName',
      dateOfBirth: 'form.dateOfBirth',
      passportInfo: 'form.passportInfo',
      fmsDate: 'form.fmsDate',
      monthIncome: 'form.monthIncome',
      sex: 'form.sex',
      pension: 'form.pension',
      socialStatus: 'form.socialStatus',
      organizationName: 'form.organizationName',
      isExactlyPensioner: 'form.isExactlyPensioner',
      showCredits: 'modals.showCredits',
      workInsurance: 'additionalServices.workInsurance',
      showDocument: 'showDocument',
    }),
    ...mapGetters(['FIO']),
  },
  watch: {
    pension(val) {
      if (!val) {
        this.socialStatus = SOCIAL_STATUSES.WORKS;
        this.isExactlyPensioner = false;
      } else {
        this.organizationName = '';
        this.isExactlyPensioner = false;
        if (this.workInsurance) {
          this.$store.dispatch('removeService', 'workInsurance');
        }
      }
    },
  },
  methods: {
    click() {
      this.$v.$touch();
      if (this.FIO.firstName && this.$v.sex.$error) {
        this.showGender = true;
      }
      if (this.$v.$invalid) {
        return;
      }
      this.showCredits = false;
      this.$router.push('product');
    },
    showModal(name) {
      document.body.style.overflow = 'hidden';
      this.document = name;
      this.showDocument = true;
    },
    closeModal() {
      document.body.style.overflow = '';
      this.showDocument = false;
    },
    setGender(suggestion) {
      /**
       * Проставляем пол клиента.
       * Если есть вариант
       * проверяем наличие пола и выставляем его
       * иначе показываем поле выбора пола.
       * Если вариантов нету
       * показываем поле выбора пола.
       */
      if (suggestion && suggestion.data) {
        const { gender } = suggestion.data;
        if (gender !== GENDER_NOT_FOUND) {
          this.sex = getFirstLetter(gender).toLowerCase();
        } else {
          this.showGenderField();
        }
      } else {
        this.showGenderField();
      }
    },
    onFIOBlur(suggestion) {
      this.$v.clientName.$touch();
      this.setGender(suggestion);
    },
    showGenderField() {
      if (this.FIO.firstName) {
        this.showGender = true;
        this.$v.sex.$touch();
      }
    },
  },
};
</script>

<style lang="scss">
  .personal-data {
    &__agreement-text {
      span {
        color: $green_1;
      }
    }

    &__link-text {
      font-weight: 600;
      display: inline-flex;
    }

    .online-checkbox {
      &__inner {
        margin-top: 20px;
      }
    }

    &__agreement {
      .online-checkbox {
        &__inner {
          margin-top: 0;
        }
      }
    }

    @include media-breakpoint-up(lg) {
      &__inline-section {
        .form-item:first-child {
          flex-grow: 1;
          max-width: 445px;
          margin-right: 42px;
        }
      }
    }
  }
</style>
