<template>
  <div class="navigation-menu">
    <div class="navigation-menu__list">
      <div
        v-for="step in steps"
        :key="step.id"
        class="navigation-menu__item"
        :class="{
          _active: step.id === currentRouteId,
          _visited: step.id < currentRouteId
        }"
        @click="click(step.id, step.name)"
      >
        {{ step.title }}
      </div>
    </div>
  </div>
</template>

<script>
import { FORM_TYPE_FULL, FORM_TYPE_SHORT } from './modules';

export default {
  name: 'NavigationMenu',
  computed: {
    currentRouteId() {
      return this.$route.meta.id;
    },
    steps() {
      // В меню показываем текущую страницу + 2 предыдущих и 3 последующих
      const { modules } = this;
      const index = modules.findIndex(module => module.id === this.currentRouteId) + 1;
      // тут 3 нужно чтобы отображать текущую и 2 предыдущих страницы.
      // особенность работы Array.slice()
      const PREVIOUS_PAGES = 4;
      const NEXT_PAGES = 3;
      const start = Math.max(index - PREVIOUS_PAGES, 0);
      const end = index + NEXT_PAGES;
      return modules.slice(start, end);
    },
    modules() {
      const { formType } = this.$store.state;
      return formType === 'short' ? FORM_TYPE_SHORT : FORM_TYPE_FULL;
    },
  },
  methods: {
    click(id, name) {
      if (id < this.currentRouteId) {
        this.$router.push({ name });
      }
    },
  },
};
</script>

<style lang="scss">
  .navigation-menu {
    margin-top: 37px;

    &__item {
      position: relative;
      display: block;
      margin-bottom: 20px;
      padding-top: 2px;
      padding-bottom: 2px;
      padding-left: 25px;
      font-size: 14px;
      font-weight: 600;
      line-height: 17px;
      letter-spacing: .02em;
      text-decoration: none;
      color: $gray_1;
      pointer-events: none;

      &._visited {
        pointer-events: all;
      }

      &.router-link-active,
      &._active,
      &._visited {
        color: $dark;
        cursor: pointer;
      }

      &.router-link-exact-active,
      &._active {
        &::before {
          content: '';
          position: absolute;
          left: -5px;
          top: 5px;
          display: inline-block;
          padding: 5px;
          border: solid #000;
          border-width: 0 2px 2px 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
</style>
