<template>
  <div
    class="pos-service"
    :class="{ _selected: getServiceValue }"
  >
    <div class="pos-service__inner">
      <div class="pos-service__header">
        <div
          class="pos-service__title"
          @click="titleClick"
          v-html="service.title"
        />
        <pos-checkbox
          :checked="getServiceValue"
          :inverse-theme="true"
          @change="checked => $emit('change', checked)"
        />
      </div>

      <div
        class="pos-service__body"
        v-html="service.description"
      />

      <div class="pos-service__footer">
        <div
          class="pos-service__text"
          v-html="service.text"
        />
        <div class="pos-service__price-per-month">
          {{ service.pricePerMonth }} ₽/ мес
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PosCheckbox from '@/components/Checkbox';

export default {
  name: 'Service',
  components: { PosCheckbox },
  props: {
    service: {
      type: Object,
      required: true,
    },
  },
  computed: {
    getServiceValue() {
      return this.$store.state.additionalServices[this.service.name];
    },
  },
  methods: {
    titleClick() {
      window.open('https://google.com', '_blank');
    },
  },
};
</script>

