<template>
  <section class="page-wrapper">
    <!-- Progress bar for tablet/mobile -->
    <mq-layout :mq="['mobile', 'tablet']">
      <div
        v-if="showCredits"
        class="credit-conditions-wrapper"
      >
        <credit-conditions @close="showCredits = false" />
      </div>
      <aside-block-provider v-slot="{ show }">
        <div v-if="show">
          <div class="responsive-panel">
            <div
              class="page-wrapper__back-button"
              :class="{ '_disabled' : $route.name === 'personal'}"
              @click="backToPreviousPage"
            >
              Назад
            </div>
            <!-- Stepper -->
            <pos-stepper />
          </div>
          <!-- Stepper modal -->
          <stepper-modal />
        </div>
      </aside-block-provider>
    </mq-layout>
    <mq-layout mq="desktop">
      <div class="page-wrapper__security">
        <div class="row">
          <div
            class="col-lg-11 offset-1"
          >
            <security-text-provider
              v-slot="{ show }"
            >
              <div
                v-if="show"
                class="page-wrapper__security-text"
              >
                Гарантируем безопасность персональных данных
              </div>
            </security-text-provider>
          </div>
        </div>
      </div>
    </mq-layout>
    <div class="page-wrapper__main">
      <div class="row">
        <div
          class="col-md-12"
          :class="{
            'offset-1': $mq === 'desktop' && !showStatusPage,
            'col-lg-12' : showStatusPage,
            'col-lg-7' : !showStatusPage,
          }"
        >
          <!-- Main slot -->
          <slot />
        </div>

        <mq-layout
          mq="desktop"
          class="col-lg-4"
        >
          <aside-block-provider v-slot="{ show }">
            <div
              v-show="show"
              class="page-wrapper__aside"
            >
              <!-- TotalSum -->
              <!-- Stepper -->
              <!-- Navigation menu -->
              <aside-block />
            </div>
          </aside-block-provider>
        </mq-layout>
      </div>
    </div>
  </section>
</template>

<script>
import PosStepper from '@/components/Stepper';
import AsideBlock from '@/components/AsideBlock';
import StepperModal from '@/components/StepperModal';
import CreditConditions from '@/components/CreditConditions';
import { mapFields } from 'vuex-map-fields';

export default {
  name: 'PageWrapper',
  components: {
    PosStepper,
    AsideBlock,
    StepperModal,
    CreditConditions,
  },
  computed: {
    showStatusPage() {
      const routeName = this.$route.name;
      if (!routeName) return false;
      return routeName.includes('status');
    },
    previousRoutePath() {
      const { routes } = this.$router.options;
      const prevRouteId = this.$route.meta.id - 1;
      if (this.$route.name !== 'personal') {
        return routes.find(route => route.meta.id === prevRouteId).path;
      }
      return '';
    },
    ...mapFields(['modals.showCredits']),
  },
  methods: {
    backToPreviousPage() {
      const route = this.$route.name === 'passport'
        ? 'contacts'
        : this.previousRoutePath;

      this.$router.push(route);
    },
  },
};
</script>

