<template>
  <div
    v-click-outside="close"
    class="pos-suggestions"
  >
    <div
      class="pos-suggestions__activator"
      @click="open"
      @keydown.up.prevent.exact="onArrowUp"
      @keydown.down.prevent.exact="onArrowDown"
      @keydown.enter.prevent.exact="select(suggestions[highlightedIndex])"
    >
      <pos-input
        ref="inputRef"
        :placeholder="placeholder"
        :value="value"
        :title="title"
        :error="error"
        :mask="mask"
        :valid="valid"
        :fio-pattern="suggestionPattern"
        :pattern="pattern"
        :translate="translate"
        :type="type"
        @input="onInput"
        @blur="onBlur"
      />
    </div>
    <div
      v-show="suggestions.length && show"
      class="pos-suggestions__suggestions"
    >
      <div
        ref="list"
        class="pos-suggestions__inner"
      >
        <div
          v-for="(suggestion, index) in suggestions"
          :key="index"
          class="pos-suggestions__suggestion js-suggestion"
          :class="{
            _active: index === highlightedIndex,
            '_multiple-row': multipleRow,
          }"
          @click="select(suggestion)"
        >
          <div class="pos-suggestions__value">
            <template>
              {{
                isTypeFmsUnit && suggestion.data
                  ? suggestion.data.code
                  : suggestion.value
              }}
            </template>
          </div>
          <div
            v-if="multipleRow && suggestion.data"
            class="pos-suggestions__secondary-value"
          >
            {{ getSecondaryValue(suggestion) }}
          </div>
        </div>
      </div>
      <div
        v-if="manualAddress"
        class="pos-suggestions__no-address"
        @click="onManualAddressClick"
      >
        Здесь нет моего адреса
      </div>
    </div>
  </div>
</template>

<script>
import getSuggestions from '@/services/dadata';

import PosInput from '@/components/Input';

const SUGGESTIONS_TYPES = {
  FIO: 'fio',
  ADDRESS: 'address',
  PARTY: 'party',
  FMS_UNIT: 'fms_unit',
};

export default {
  name: 'Suggestions',
  components: { PosInput },
  inheritAttrs: false,
  model: {
    prop: 'value',
    event: 'input',
  },
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    error: Boolean,
    mask: {
      type: [String, Object],
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    pattern: {
      type: String,
      default: '',
    },
    // Тип подсказки
    type: {
      type: String,
      required: true,
      validator: val => [
        'fio',
        'address',
        'party',
        'fms_unit',
      ].includes(val),
    },
    count: {
      type: [Number, String],
      default: 10,
    },
    valid: Boolean,
    translate: Boolean,
    // ручной ввод отдельных полей
    manualAddress: Boolean,
  },
  data() {
    return {
      suggestions: [],
      show: false,
      highlightedIndex: 0,
    };
  },
  computed: {
    isTypeFmsUnit() {
      return this.type === SUGGESTIONS_TYPES.FMS_UNIT;
    },
    isTypeFio() {
      return this.type === SUGGESTIONS_TYPES.FIO;
    },
    isTypeParty() {
      return this.type === SUGGESTIONS_TYPES.PARTY;
    },
    multipleRow() {
      return this.isTypeFmsUnit || this.isTypeParty;
    },
    suggestionPattern() {
      const matchedValue = this.suggestions.some(item => item.value === this.value);

      if (this.value && !matchedValue && this.suggestions.length && this.isTypeFio) {
        const suggestionValue = this.suggestions[0].value;
        return suggestionValue.slice(this.value.length, suggestionValue.length);
      } return '';
    },
  },
  watch: {
    highlightedIndex(val) {
      if (val) {
        this.scrollList(val);
      }
    },
    value(val) {
      if (val.length === 0) {
        this.$emit('field-cleared');
      }
    },
  },
  methods: {
    onInput(value) {
      this.resetHighlightedIndex();
      this.open();
      const params = {
        query: value || '',
        count: +this.count,
      };
      if (this.isTypeFio) {
        params.parts = ['SURNAME', 'NAME', 'PATRONYMIC'];
      }
      this.updateValue(value);
      // отправить запрос в Dadata
      getSuggestions(this.type, params)
        .then((result) => {
          this.suggestions = result.suggestions || [];
          this.$emit('open');
        });
    },
    onBlur() {
      this.$emit('blur', this.suggestions[0] || []);
    },
    highlight(index) {
      if (!this.show) {
        return;
      }
      if (index <= 0) {
        this.resetHighlightedIndex();
        return;
      }
      if (index >= this.suggestions.length) {
        this.highlightedIndex = this.suggestions.length - 1;
      }
    },
    setCaretPosition(element, pos) {
      element.focus();
      element.setSelectionRange(pos, pos);
    },
    select(suggestion) {
      if (this.isTypeFio && suggestion) {
        this.updateValue(`${this.getSuggestionValue(suggestion)} `);
      } else if (suggestion) {
        this.updateValue(this.getSuggestionValue(suggestion));
      }
      this.close();
      this.$nextTick(() => {
        this.setCaretPosition(this.$refs.inputRef.$refs.input, this.value.length);
      });
      this.resetHighlightedIndex();
      this.$emit('select', suggestion);
    },
    open() {
      this.show = true;
    },
    close() {
      this.show = false;
    },
    updateValue(value) {
      this.$emit('input', value);
    },
    getSuggestionValue(suggestion) {
      // Исключение для поля "Код подразделения"
      // т.к. api подсказок ("кем выдан паспорт") в поле value возвращает только название органа
      if (this.isTypeFmsUnit) {
        return suggestion.data.code;
      }
      return suggestion.value;
    },
    getSecondaryValue(suggestion) {
      if (this.isTypeFmsUnit) {
        return suggestion.value;
      }
      if (this.isTypeParty) {
        return suggestion.data.address.value;
      }
      return '';
    },
    // Функция для скролла списка c клавиатуры, если елемент находится вне зоны видимости
    scrollList() {
      const { list } = this.$refs;
      const el = list.querySelectorAll('.js-suggestion')[this.highlightedIndex];
      const { offsetHeight: listHeight, scrollTop: listTop } = list;
      const listBottom = listHeight + listTop;
      const { offsetHeight: elHeight, offsetTop: elTop } = el;
      const elBottom = elHeight + elTop;
      if (elBottom > listBottom || elTop < listTop) {
        list.scrollTo(0, elBottom - listHeight);
      }
    },
    onArrowUp() {
      if (this.highlightedIndex <= 0) return;
      this.highlightedIndex -= 1;
      this.highlight(this.highlightedIndex);
    },
    onArrowDown() {
      if (this.show && this.suggestions.length !== 0) {
        this.highlightedIndex += 1;
        this.highlight(this.highlightedIndex);
      }
    },
    resetHighlightedIndex() {
      this.highlightedIndex = 0;
    },
    onManualAddressClick() {
      this.close();
      this.$emit('no-address');
    },
  },
};
</script>

<style lang="scss">
  .pos-suggestions {
    position: relative;

    &__suggestions {
      position: absolute;
      z-index: 1;
      left: 0;
      top: calc(100% + 4px);
      width: 100%;
      border: 1px solid $gray_5;
      border-radius: 4px;
      background-color: $white;
      box-shadow: 0 4px 16px rgba(0, 0, 0, .1);
    }

    &__inner {
      max-height: 222px;
      overflow-y: auto;
    }

    &__suggestion {
      padding: 17px 24px;
      cursor: pointer;

      &._multiple-row {
        padding-top: 6px;
        padding-bottom: 10px;
      }

      &._active,
      &:hover {
        background-color: $gray_7;
      }
    }

    &__value {
      font-size: 15px;
      line-height: 140%;
      color: $dark;
      pointer-events: none;
    }

    &__secondary-value {
      font-size: 13px;
      line-height: 140%;
      color: $gray_1;
      pointer-events: none;
    }

    &__no-address {
      padding: 10px 24px;
      color: $gray_2_darker;
      text-decoration: underline;
      cursor: pointer;
    }
  }
</style>
