import { ROUTE_NAMES } from '@/utils/constants';

export default {
  name: 'AsideBlockProvider',
  computed: {
    show() {
      const PAGES = [
        ROUTE_NAMES.CANCEL_PAGE,
        ROUTE_NAMES.SMS_PAGE,
        ROUTE_NAMES.SUCCESS_PAGE,
        ROUTE_NAMES.STATUS_SUCCESS,
        ROUTE_NAMES.STATUS_FAIL,
        ROUTE_NAMES.STATUS_SEND,
        ROUTE_NAMES.STATUS_FINISH,
      ];

      return !PAGES.includes(this.$route.name);
    },
  },
  render() {
    return this.$scopedSlots.default({
      show: this.show,
    });
  },
};
