<template>
  <div class="radio-list">
    <pos-radio
      v-for="item in items"
      :key="item.id"
      :name="name"
      :value="item.value"
      :selected-value="selectedValue"
      :is-selected="item.value === selectedValue"
      @change="onChange(item.value)"
    >
      <slot :item="item" />
    </pos-radio>
  </div>
</template>

<script>
import PosRadio from './Radio';

export default {
  name: 'RadioList',
  components: { PosRadio },
  model: {
    prop: 'selectedValue',
    event: 'input',
  },
  props: {
    items: {
      type: Array,
      default: () => [],
    },
    selectedValue: {
      type: String,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
  },
  methods: {
    onChange(value) {
      this.$emit('input', value);
    },
  },
};
</script>
