<template>
  <div
    class="pos-input"
    :class="classes"
  >
    <label class="pos-input__control">
      <slot>{{ title }}</slot>
    </label>
    <div class="pos-input__container">
      <span
        v-if="prefix"
        class="pos-input__prefix"
      >
        {{ prefix }}
      </span>
      <span
        class="pos-input__text"
        role="presentation"
      >
        <span>{{ value }}</span>
        {{ currentPattern }}
      </span>
      <input
        ref="input"
        v-mask="mask"
        class="pos-input__field-slot"
        :value="value"
        :type="type"
        :disabled="disabled"
        v-bind="$attrs"
        @input="onInput"
        @change="onChange"
        @focus="onFocus"
        @blur="onBlur"
      >
    </div>
    <div
      v-if="$slots['suffix']"
      class="pos-input__suffix"
    >
      <slot name="suffix" />
    </div>
    <div
      v-if="$slots['maskIcon']"
      class="pos-input__icon"
    >
      <slot name="maskIcon" />
    </div>
    <div
      v-if="valid"
      class="pos-input__valid"
    />
  </div>
</template>
<script>
import { switchLanguage } from '@/utils/languageSwitcher';

export default {
  name: 'PosInput',
  inheritAttrs: false,
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    type: {
      type: String,
      default: 'text',
    },
    title: {
      type: String,
      default: '',
    },
    mask: {
      type: [String, Object],
      default: '',
    },
    pattern: {
      type: String,
      default: '',
    },
    prefix: {
      type: String,
      default: '',
    },
    fioPattern: {
      type: String,
      default: '',
    },
    error: Boolean,
    valid: Boolean,
    disabled: Boolean,
    forDropdown: Boolean,
    translate: Boolean,
  },
  data() {
    return {
      focused: false,
    };
  },
  computed: {
    classes() {
      return {
        _focused: this.focused,
        _disabled: this.disabled,
        _error: this.error,
        _valid: this.valid,
        '_has-value': this.value === 0 || !!this.value,
        '_with-suffix': this.$slots.suffix || this.valid,
        '_with-icon': this.$slots.maskIcon,
        '_for-dropdown': this.forDropdown,
        '_with-prefix': this.prefix,
      };
    },
    maskPattern() {
      if (!this.value) {
        return '';
      }
      return this.pattern.slice(this.value.length, this.pattern.length);
    },
    currentPattern() {
      return this.fioPattern || this.maskPattern;
    },
  },
  methods: {
    onInput(e) {
      this.emitValue(e, 'input');
    },
    onChange(e) {
      this.emitValue(e, 'change');
    },
    onFocus() {
      this.focused = true;
      this.$emit('focus');
    },
    onBlur() {
      this.focused = false;
      this.$emit('blur');
    },
    emitValue(event, eventName) {
      let val = event.target.value;
      if (this.translate) {
        val = switchLanguage(val);
      }
      this.$emit(eventName, val);
    },
  },
};
</script>

<style lang="scss">
  $transition-duration: .2s;
  $transition-timing-function: cubic-bezier(.4, 0, .2, 1);

  $left-indent: 22px;
  $top-indent: 27px;

  .pos-input {
    position: relative;
    font-size: $main-text;
    line-height: $line-height;
    text-align: left;
    background: $input-bg-color;
    border: 1px solid transparent;
    box-sizing: border-box;
    border-radius: $input-border-radius;
    width: 100%;
    padding-right: 48px;

    &__control {
      position: absolute;
      left: $input-indent;
      right: $input-indent;
      transform: translateY(-50%);
      top: 50%;
      pointer-events: none;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      transition: all $transition-duration $transition-timing-function;
      color: $input-text-color;
      font-size: $main-text;
      line-height: $line-height;
      transform-origin: left top;
      padding-right: 25px;
    }

    &__container {
      position: relative;
      padding: 25px 5px 13px $left-indent;
    }

    &__field-slot {
      border: 0;
      padding: 0;
      box-sizing: border-box;
      outline: none;
      line-height: 26px;
      width: 100%;
      background: transparent;

      &:hover {
        color: transparent;
      }

      &::placeholder {
        opacity: 0;
      }
    }

    &__text {
      position: absolute;
      color: $gray_11;
      pointer-events: none;
      z-index: -1;
      top: $top-indent;
      white-space: nowrap;

      span {
        color: transparent;
        opacity: 0;
        visibility: hidden;
        margin-left: -4px;
      }
    }

    &__value {
      position: absolute;
      pointer-events: none;
      left: 50px;
      top: $top-indent;
    }

    &__prefix {
      position: absolute;
      top: $top-indent;
      display: none;
    }

    &._focused,
    &._has-value {
      background: transparent;
      color: $input-text-color-active;

      .pos-input__control {
        transform: translateY(-27px);
        font-size: 11px;
      }

      .pos-input__field-slot:hover {
        color: $input-text-color-active;
      }

      .pos-input__field-slot::placeholder {
        opacity: 1;
        color: $gray_11;
      }

      .pos-input__icon {
        opacity: 1;
      }

      .pos-input__prefix {
        display: block;
      }
    }

    &._error {
      border-color: $red_2;

      .pos-input__control {
        color: $red_2;
      }
    }

    &._for-dropdown._focused {
      .pos-input__control {
        width: auto;
        font-size: 16px;
        transform: translateY(-50%);
        color: $dark;
      }
    }

    &._for-dropdown._has-value {
      .pos-input__control {
        transform: translateY(-27px);
        font-size: 11px;
      }
    }

    &._for-dropdown &__field-slot {
      cursor: pointer;
    }

    &._has-value {
      border-color: $input-border-color-onblur;
    }

    &._focused {
      border-color: $input-text-color-active;
    }

    &._error {
      &._has-value,
      &._focused {
        border-color: $red_2;

        &.pos-input__control {
          color: $red_3;
        }
      }
    }

    &._valid {
      &._has-value,
      &._focused {
        .pos-input__valid {
          opacity: 1;
          background: url("data:image/svg+xml;charset=utf-8,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20 6L9 17l-5-5' stroke='%235EC738' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") center center no-repeat;
        }
      }
    }

    &._disabled {
      background: $input-bg-disabled;
      color: $input-text-color;
    }

    &__suffix,
    &__icon,
    &__valid {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-self: stretch;
      width: 48px;
      height: 100%;
      pointer-events: none;
    }

    &__icon {
      line-height: $line-height;
      font-size: 22px;
    }

    &._focused {
      .pos-input__icon {
        color: $gray_2_darker;
      }
    }

    &__icon,
    &__valid {
      opacity: 0;
    }

    &._with-prefix {
      .pos-input__field-slot,
      .pos-input__text {
        padding-left: $left-indent;
      }
    }

    @include media-breakpoint-up(lg) {
      border: 1px solid $input-border-color;
    }
  }
</style>
