<template>
  <div
    class="form-item"
    :class="classes"
  >
    <div class="form-item__body">
      <div class="form-item__content">
        <slot />
      </div>
      <div
        v-if="isDescriptionVisible && inline"
        class="form-item__description _inline"
      >
        {{ description }}
        <slot name="description" />
      </div>
    </div>
    <div
      v-if="error && withSlottedError"
      class="form-item__error"
    >
      {{ error }}
      <slot name="error" />
    </div>
    <div
      v-else-if="isDescriptionVisible && !inline"
      class="form-item__description _bottom"
    >
      {{ description }}
      <slot name="description" />
    </div>
    <slot name="additional-description" />
  </div>
</template>

<script>
export default {
  name: 'FormItem',
  props: {
    description: {
      type: String,
      default: '',
    },
    error: {
      type: String,
      default: '',
    },
    withSlottedDescription: Boolean,
    withSlottedError: Boolean,
    inline: Boolean,
    noMarginBottom: Boolean,
  },
  computed: {
    isDescriptionVisible() {
      return this.description || this.withSlottedDescription;
    },
    classes() {
      return {
        _inline: this.inline,
        '_no-mb': this.noMarginBottom,
        '_no-caption': !this.isDescriptionVisible,
      };
    },
  },
};
</script>

<style lang="scss">
  .form-item {
    margin-bottom: $main-indent;

    &._no-mb {
      margin-bottom: 0;
    }

    &._no-caption {
      margin-bottom: 32px;
    }

    &__description,
    &__error {
      margin-top: 8px;
      font-size: 12px;
      line-height: 15px;
    }

    &__description {
      color: $black_1;
    }

    &__error {
      color: $red_2;
    }

    &._inline {
      .form-item__body {
        display: flex;
        align-items: center;
      }

      .form-item__description {
        margin-left: 24px;
        line-height: 21px;
      }
    }

    @include media-breakpoint-down(sm) {
      margin-bottom: 16px;

      &__description,
      &__error {
        font-size: 10px;
        line-height: 12px;
      }

      &._inline {
        .form-item__description {
          margin-left: 24px;
          line-height: 21px;
        }
      }
    }
  }
</style>
