/* https://www.npmjs.com/package/mdn-polyfills */
import 'mdn-polyfills/Array.prototype.findIndex';
import 'mdn-polyfills/Array.prototype.includes';

import Vue from 'vue';
import VueMq from 'vue-mq';
import vClickOutside from 'v-click-outside';
import Vuelidate from 'vuelidate';
import VueAwesomeSwiper from 'vue-awesome-swiper';
import PortalVue from 'portal-vue';

import App from './App.vue';
import router from './router';
import store from './store';

import '@/components/providers';
import { ROUTE_IDS } from '@/utils/constants';
import mask from '@/utils/mask';
import error from '@/utils/validationErrorText';
import pattern from '@/utils/pattern';
import 'swiper/css/swiper.css';

const VueInputMask = require('vue-inputmask').default;

Vue.config.productionTip = false;

Vue.prototype.$$ROUTE_IDS = ROUTE_IDS;
Vue.prototype.$$mask = mask;
Vue.prototype.$$error = error;
Vue.prototype.$$pattern = pattern;

Vue.use(VueMq, {
  breakpoints: {
    mobile: 450,
    tablet: 992,
    desktop: 1250,
  },
  // customize this for SSR
  defaultBreakpoint: 'sm',
});
Vue.use(vClickOutside);
Vue.use(Vuelidate);
Vue.use(VueInputMask);
Vue.use(VueAwesomeSwiper);
Vue.use(PortalVue);

new Vue({
  VueMq,
  router,
  store,
  render: h => h(App),
}).$mount('#app');
