/**
 * Заменить плейсхолдеры
 * @param placeholdersAndValues [ { placeholder: String, value: any }]
 * @param str - строка для поиска
 * @return {string}
 */
export const replaceByPlaceholder = (placeholdersAndValues, str) => placeholdersAndValues
  .reduce((acc, { placeholder, value }) => acc.replace(placeholder, value), str);

/**
 * Вернуть первую букву строки
 * @param string
 * @returns {string}
 */
export const getFirstLetter = (string = '') => string.charAt(0);

/**
 * Возвращает нужный вариант слова из списка значений
 * @param {number} value - число
 * @param {Array.<string>} plurals - варианты
 * @example [услуга, услуги, услуг]
 * @returns {string}
 */
export const getPlural = (value, plurals) => {
  if (typeof plurals === 'string' || plurals.length === 1) {
    return plurals;
  }
  const n = Math.abs(value);
  // See http://docs.translatehouse.org/projects/localization-guide/en/latest/l10n/pluralforms.html?id=l10n/pluralforms
  // eslint-disable-next-line no-nested-ternary
  const i = n % 10 === 1 && n % 100 !== 11
    ? 0
    : n % 10 >= 2 && n % 10 <= 4 && (n % 100 < 10 || n % 100 >= 20)
      ? 1
      : 2;
  return plurals[i];
};

/**
 * Возвращает конкатенированную строку адреса
 * @param city
 * @param settlementName
 * @param street
 * @param house
 * @param doorway
 * @param apartment
 * @returns {string}
 */
export const createAddressString = (
  city, settlement, settlementName,
  street, streetName, house, doorway, apartment,
) => {
  const sCity = city ? `г ${city}, ` : '';
  const sSettlement = settlement ? `${settlement} ` : '';
  const sSettlementТName = settlementName ? `${settlementName}, ` : '';
  const sStreet = street ? ` ${street} ` : '';
  const sHouse = house ? ` д ${house},` : '';
  const sDoorway = doorway ? ` к ${doorway},` : '';
  const sApartment = apartment ? ` кв ${apartment}` : '';
  return `${sCity}${sSettlement}${sSettlementТName}${sStreet}${streetName}${sHouse}${sDoorway}${sApartment}`;
};
