<template>
  <label
    class="online-checkbox"
    :class="classes"
  >
    <input
      tabindex="-1"
      type="checkbox"
      class="online-checkbox__control"
      v-bind="$attrs"
      :checked="checked"
      :disabled="disabled"
      v-on="listeners"
    >
    <span class="online-checkbox__inner">
      <span
        tabindex="0"
        class="online-checkbox__box"
      >
        <icon-check
          v-if="!disabled"
          class="online-checkbox__check"
        />
      </span>
      <span class="online-checkbox__content">
        <slot />
      </span>
    </span>
  </label>
</template>

<script>
import IconCheck from './IconCheck';

export default {
  name: 'Checkbox',
  components: { IconCheck },
  inheritAttrs: false,
  model: {
    prop: 'checked',
    event: 'change',
  },
  props: {
    checked: Boolean,
    invalid: Boolean,
    disabled: Boolean,
    boldFont: Boolean,
    // отдельный пропс для страницы продукта
    inverseTheme: Boolean,
  },
  computed: {
    classes() {
      return {
        _checked: this.checked,
        _disabled: this.disabled,
        _error: this.invalid,
        '_bold-font': this.boldFont && this.checked,
        '_has-content': this.$slots.default,
        _inverse: this.inverseTheme,
      };
    },
    listeners() {
      return {
        ...this.$listeners,
        change: e => this.$emit('change', e.target.checked),
      };
    },
  },
};
</script>

<style lang="scss">
  .online-checkbox {
    position: relative;
    display: block;
    user-select: none;

    &__control {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
    }

    &__inner {
      @include font-set(body-5, regular);
      color: $dark;
      display: flex;
      cursor: pointer;
    }

    &__box {
      width: 24px;
      height: 24px;
      border-radius: 4px;
      border: 1px solid $gray_3;
      flex-shrink: 0;
      position: relative;
      background: $white;
    }

    &__check {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: $white;
      opacity: 0;
      stroke: currentColor;
    }

    &__content {
      font-size: 14px;
      line-height: 21px;
      align-self: center;

      @include media-breakpoint-up(lg) {
        font-size: 14px;
      }
    }

    // bold-font
    &._bold-font {
      .online-checkbox__content {
        font-weight: 500;
      }
    }

    // checked
    &__control:checked ~ &__inner &__check {
      opacity: 1;
    }

    &__control:checked ~ &__inner &__box {
      border-color: $gray_2;
    }

    // disabled
    &__control:disabled ~ &__inner {
      cursor: default;
    }

    &__control:disabled ~ &__inner &__box {
      border-color: $gray_5;
      background-color: $gray_6;
    }

    &__control:disabled ~ &__inner &__check {
      opacity: 0;
    }

    // error
    &._error {
      .online-checkbox__box {
        border-color: $red_2;
      }

      .online-checkbox__content {
        color: $red_2;

        .link-text {
          color: $dark;
        }
      }
    }

    // has-content
    &._has-content &__box {
      margin-right: 12px;

      @include media-breakpoint-up(lg) {
        margin-right: 16px;
      }
    }

    // inverse
    &._inverse {
      .online-checkbox__box {
        border-color: #4a4a4a;
      }
    }

    &._checked._inverse {
      .online-checkbox__box {
        border-color: $gray_10;
      }
    }
  }
</style>
