// @ts-check
export const CalculatorUtils = {
  /**
   * @param {{ratePerMonthInPercents: number, termMonth: number}} params
   * @returns {number}
   */
  getAnnuityRatio({ ratePerMonthInPercents, termMonth }) {
    const rate = ratePerMonthInPercents / 100;
    const a = (1 + rate) ** termMonth;
    return (rate * a) / (a - 1);
  },
  /**
   * @param {{sumMonthIncome: number, monthMyCreditPayment: number}} params
   * @returns {number}
   */
  getNetIncome({ sumMonthIncome, monthMyCreditPayment }) {
    return sumMonthIncome / 2 - monthMyCreditPayment;
  },
  /**
   * @param {{sumMonthIncome: number}} params
   * @returns {number}
   */
  getMonthMyCreditPaymentMax({ sumMonthIncome }) {
    const MIN_NET_INCOME = 5000;
    /** @see getNetIncome implementation */
    return Math.floor(sumMonthIncome / 2 - MIN_NET_INCOME);
  },
  /**
   * @param {{sumMonthIncome: number, monthMyCreditPayment: number, annuityRatio: number}} params
   * @returns {number}
   */
  getSumLoanMax({ sumMonthIncome, monthMyCreditPayment, annuityRatio }) {
    const a = this.getNetIncome({
      sumMonthIncome,
      monthMyCreditPayment,
    }) / annuityRatio;
    const ROUNDING_THRESHOLD = 50000;
    return Math.floor(a / ROUNDING_THRESHOLD) * ROUNDING_THRESHOLD;
  },
  /**
   * @param {{sumLoan, annuityRatio}} params
   * @returns {number}
   */
  getPreQualifiedMonthPayment({ sumLoan, annuityRatio }) {
    return sumLoan * annuityRatio;
  },
  /**
   * @param {Array<Api.CreditCalculationParameters>} tariffs
   * @returns {{rateInPercents: number; termMonthMax: number}}
   */
  getDefaultParamsForFirstCalculation(tariffs) {
    const rateInPercents = Math.max(...tariffs.map(item => item.ratePercent));
    const termMonthMax = Math.min(...tariffs.map(item => item.termMonthMax));

    return {
      rateInPercents,
      termMonthMax,
    };
  },
  /**
   * @param {number} sumLoan
   * @param {Array<Api.CreditCalculationParameters>} tariffs
   * @returns {{rateInPercents: number; termMonthMax: number}}
   */
  getCorrectedCreditCalculationParameters(sumLoan, tariffs) {
    const { ratePercent, termMonthMax } = tariffs
      .find(item => item.sumLoanMin <= sumLoan && sumLoan <= item.sumLoanMax);

    return {
      rateInPercents: ratePercent,
      termMonthMax,
    };
  },
  /**
   * @param {Array<Api.CreditCalculationParameters>} tariffs
   * @returns {number}
   */
  getSumLoanMinForAllTariffs(tariffs) {
    return Math.min(...tariffs.map(item => item.sumLoanMin));
  },
  /**
   * @param {Array<Api.CreditCalculationParameters>} tariffs
   * @returns {number}
   */
  getSumLoanMaxForAllTariffs(tariffs) {
    return Math.max(...tariffs.map(item => item.sumLoanMax));
  },
  /**
   * @param {Array<Api.CreditCalculationParameters>} tariffs
   * @returns {number}
   */
  getTermMonthMinForAllTariffs(tariffs) {
    return Math.min(...tariffs.map(item => item.termMonthMin));
  },
};
