/**
 * В этом файле собраны функции
 * которые проверяют доступность страховок и коробочных услуг
 * @param {number} age - возраст клиента
 * @param {string} sex - пол клиента
 * @param {boolean} isPensioner - является ли клиент пенсионером
 * @returns {boolean}
 */

const MIN_AGE = 18;

/* Проверка доступности страховки жизни */
const availableLifeInsurance = (age) => {
  const MAX_AGE = 69;
  return age >= MIN_AGE && age <= MAX_AGE;
};

/* Проверка доступности страховки работы */
const availableWorkInsurance = (age, sex, isPensioner) => {
  if (isPensioner) {
    return false;
  }
  const MAX_MALE_AGE = 60;
  const MAX_FEMALE_AGE = 55;
  const MALE = 'm';
  const MAX_AGE = sex === MALE ? MAX_MALE_AGE : MAX_FEMALE_AGE;

  return age >= MIN_AGE && age <= MAX_AGE;
};

/* Проверка доступности коробочных услуг */
const availableBoxInsurance = age => age >= MIN_AGE;

export default {
  availableLifeInsurance,
  availableWorkInsurance,
  availableBoxInsurance,
};
