<template>
  <div class="pos-products">
    <div class="pos-products__wrapper">
      <product
        v-for="(product, index) in products"
        :key="index"
        :product="product"
        :selected="currentProduct.type === product.type"
        @select="onSelect(product)"
      />
    </div>
    <div
      v-if="productDescription"
      class="pos-products__product-description"
      :style="{'--left': currentProduct.arrowPosition}"
      @click="onDescriptionClick"
      v-html="productDescription"
    />
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';

import Product from './Product';

export default {
  name: 'Products',
  components: { Product },
  props: {
    products: {
      type: Array,
      required: true,
    },
  },
  computed: {
    ...mapFields({
      currentProduct: 'product',
    }),
    productDescription() {
      const product = this.products.find(product => product.type === this.currentProduct.type);
      return (product && product.description) || '';
    },
  },
  methods: {
    onSelect(product) {
      this.currentProduct = product;
      this.$emit('select');
    },
    onDescriptionClick(e) {
      if (e.target.id === 'first-payment') {
        this.$emit('show-first-payment');
      }
    },
  },
};
</script>

