<template>
  <label
    class="pos-radio"
    :class="classes"
  >
    <span class="pos-radio__body">
      <input
        v-model="radioButtonValue"
        class="pos-radio__input"
        type="radio"
        :name="name"
        :value="value"
        :disabled="disabled"
      >
      <span class="pos-radio__controls" />
      <span class="pos-radio__content">
        <slot />
      </span>
    </span>
  </label>
</template>

<script>
export default {
  name: 'Radio',
  props: {
    name: {
      type: String,
      default: 'cancellation-reason',
    },
    value: {
      type: String,
      required: true,
    },
    selectedValue: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    isSelected: Boolean,
  },
  computed: {
    radioButtonValue: {
      get() {
        return this.selectedValue;
      },
      set() {
        this.$emit('change', this.value);
      },
    },
    classes() {
      return {
        _selected: this.isSelected,
        _disabled: this.disabled,
      };
    },
  },
};
</script>

<style lang="scss">
  .pos-radio {
    position: relative;
    display: block;
    min-height: 24px;
    margin-bottom: 24px;
    cursor: pointer;

    &__input {
      display: none;
    }

    &__body {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      width: 100%;
      height: 100%;
      font-size: 16px;
      line-height: 24px;
    }

    &__controls {
      position: relative;
      flex-shrink: 0;
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-right: 16px;
      background-color: $white;
      border-radius: 50%;
      border: 1px solid $gray_3;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 13px;
        height: 13px;
        background-color: $green_2;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        opacity: 0;
      }
    }

    &__content {
      position: relative;
      display: inline-block;

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    &._selected {
      .pos-radio__content {
        font-weight: 600;

        &::before {
          content: none;
        }
      }
    }
  }

  .pos-radio {
    input:checked + .pos-radio__controls {
      background-color: $gray_7;
      border-color: $gray_2;

      &::after {
        opacity: 1;
      }
    }

    input:disabled + .pos-radio__controls {
      background-color: $gray_6;
      border-color: $gray_5;

      &::after {
        opacity: 0;
      }
    }
  }

  .radio-list {
    @include media-breakpoint-down(sm) {
      margin-left: 40px;
    }
  }
</style>
