<template>
  <section class="online-header">
    <div class="online-header__inner">
      <div class="online-header__left">
        <div
          class="online-header__logo"
          @click="goToMainPage"
        >
          <img
            src="@/assets/logo.svg"
            alt="logo"
          >
        </div>
        <div class="online-header__text-block">
          <div class="online-header__text-separator">
            /
          </div>
          <div class="online-header__text">
            Заявка на получение кредита
          </div>
        </div>
      </div>

      <div class="online-header__right">
        <div
          v-if="isCancelPage"
          class="online-header__action-link"
          @click="closeWindow"
        >
          закрыть окно
        </div>
        <div
          v-else
          class="online-header__action-link"
          @click="goToCancelPage"
        >
          отменить заявку
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { ROUTE_NAMES } from '@/utils/constants';

export default {
  name: 'Header',
  computed: {
    isCancelPage() {
      return this.$route.name === ROUTE_NAMES.CANCEL_PAGE;
    },
  },
  methods: {
    goToMainPage() {
      this.$router.push('/');
    },
    goToCancelPage() {
      this.$router.push('cancel');
    },
    closeWindow() {
      this.$router.go(-1);
    },
  },
};
</script>

