<template>
  <div class="passport-data">
    <form-wrapper>
      <template v-slot:info>
        Заявка отправлена в банк на предварительную проверку.
        Продолжайте заполнять <br>
        анкету для увеличения шансов на одобрение кредита.
      </template>
      <template v-slot:title>
        Паспортные данные
      </template>

      <template v-slot:default>
        <form-item
          :with-slotted-error="$v.issuerCode.$error"
          error="Укажите код подразделения"
        >
          <suggestions
            v-model="issuerCode"
            placeholder="100-010"
            pattern="___-___"
            inputmode="decimal"
            :mask="$$mask.issuerCode"
            :error="$v.issuerCode.$error"
            :valid="!$v.issuerCode.$invalid"
            title="Код подразделения"
            type="fms_unit"
            translate
            @blur="$v.issuerCode.$touch()"
            @select="setIssuerName"
          />
        </form-item>
        <form-item
          description="Проверьте, должно быть точно, как в паспорте"
          :with-slotted-error="$v.issuerName.$error"
          error="Укажите кем выдан Ваш паспорт"
        >
          <pos-input
            v-model="issuerName"
            :error="$v.issuerName.$error"
            :valid="!$v.issuerName.$invalid"
            title="Кем выдан паспорт"
            @blur="$v.issuerName.$touch()"
          />
        </form-item>

        <div class="row">
          <div class="col-lg-12">
            <form-item
              description="Укажите как в паспорте"
              :with-slotted-error="$v.placeOfBirth.$error"
              error="Укажите место рождения (как в паспорте)"
            >
              <pos-input
                v-model="placeOfBirth"
                placeholder="г. Москва"
                title="Место рождения"
                :mask="$$mask.placeOfBirth"
                :error="$v.placeOfBirth.$error"
                :valid="!$v.placeOfBirth.$invalid"
                @blur="$v.placeOfBirth.$touch()"
              />
            </form-item>
          </div>
        </div>

        <form-item
          description="Индекс ускорит ввод адреса"
          :with-slotted-error="$v.registryAddress.$error"
          :error="$$error.addressText($v.registryAddress.$error, registryAddress)"
        >
          <suggestions
            v-model="registryAddress"
            placeholder="Регион, город, улица, дом, корпус, квартира"
            title="Адрес постоянной регистрации"
            :error="$v.registryAddress.$error"
            :valid="!$v.registryAddress.$invalid"
            type="address"
            manual-address
            @blur="suggestion => onAddressBlur(
              suggestion, 'registryAddressKladrCode', 'registryAddress'
            )"
            @select="suggestion => onAddress(suggestion, 'registryAddressKladrCode')"
            @no-address="showAddressModal"
            @open="showSuggestionsList"
          />
        </form-item>

        <form-item
          v-if="!factRegistryAddressFlg"
          description="Регион, город, улица, корпус, квартира"
          :with-slotted-error="$v.factAddress.$error"
          :error="$$error.addressText($v.factAddress.$error, factAddress)"
        >
          <suggestions
            v-model="factAddress"
            placeholder="Город, улица, дом, корпус, квартира, индекс"
            title="Адрес фактического проживания"
            :error="$v.factAddress.$error"
            :valid="!$v.factAddress.$invalid"
            type="address"
            manual-address
            @blur="suggestion => onAddressBlur(
              suggestion, 'factAddressKladrCode', 'factAddress'
            )"
            @select="suggestion => onAddress(suggestion, 'factAddressKladrCode')"
            @no-address="showAddressModal"
          />
        </form-item>

        <form-item>
          <pos-checkbox v-model="factRegistryAddressFlg">
            <span class="passport-data__check-text">
              Адрес проживания совпадает с адресом регистрации
            </span>
          </pos-checkbox>
        </form-item>
      </template>

      <template v-slot:buttons>
        <pos-button
          :disabled="$v.$error"
          :is-loading="isLoading"
          @click="click"
        >
          Далее
        </pos-button>
      </template>
    </form-wrapper>

    <portal
      v-if="isShowAddressModal"
      to="modal"
    >
      <address-modal @close="closeAndResetAddressModal">
        <template #header>
          Адрес регистрации
        </template>
        <template #default>
          <div class="row">
            <div class="col-md-12 col-lg-5">
              <form-item
                :with-slotted-error="$v.registryAddressZipcode.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressZipcode"
                  title="Индекс"
                  :mask="$$mask.onlyNumberMask"
                  :error="$v.registryAddressZipcode.$error"
                  :valid="!$v.registryAddressZipcode.$invalid"
                  @blur="$v.registryAddressZipcode.$touch()"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-7">
              <form-item
                :with-slotted-error="$v.registryAddressRegion.$error"
                error="Заполните поле"
              >
                <pos-dropdown
                  v-model.trim="registryAddressRegion"
                  title="Регион"
                  :items="regionReference"
                  :error="$v.registryAddressRegion.$error"
                  :valid="!$v.registryAddressRegion.$invalid"
                  @blur="$v.registryAddressRegion.$touch()"
                />
              </form-item>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 col-lg-5">
              <form-item
                :with-slotted-error="$v.registryAddressDistrict.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressDistrict"
                  title="Район"
                  :error="$v.registryAddressDistrict.$error"
                  :valid="!$v.registryAddressDistrict.$invalid"
                  @blur="$v.registryAddressDistrict.$touch()"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-7">
              <form-item
                :with-slotted-error="$v.registryAddressTown.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressTown"
                  title="Город"
                  :error="$v.registryAddressTown.$error"
                  :valid="!$v.registryAddressTown.$invalid"
                  @blur="$v.registryAddressTown.$touch()"
                />
              </form-item>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 col-lg-5">
              <form-item
                :with-slotted-error="$v.registryAddressHumanSettlement.$error"
                error="Заполните поле"
              >
                <pos-dropdown
                  v-model.trim="registryAddressHumanSettlement"
                  title="Населенный пункт"
                  :error="$v.registryAddressHumanSettlement.$error"
                  :items="settlementType"
                  :valid="!$v.registryAddressHumanSettlement.$invalid"
                  @blur="$v.registryAddressHumanSettlement.$touch()"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-7">
              <form-item
                :with-slotted-error="$v.registryAddressSettlementName.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressSettlementName"
                  title="Название населенного пункта"
                  :error="$v.registryAddressSettlementName.$error"
                  :valid="!$v.registryAddressSettlementName.$invalid"
                  @blur="$v.registryAddressSettlementName.$touch()"
                />
              </form-item>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12 col-lg-5">
              <form-item
                :with-slotted-error="$v.registryAddressStreet.$error"
                error="Заполните поле"
              >
                <pos-dropdown
                  v-model.trim="registryAddressStreet"
                  title="Улица"
                  :error="$v.registryAddressStreet.$error"
                  :items="streetType"
                  :valid="!$v.registryAddressStreet.$invalid"
                  @blur="$v.registryAddressStreet.$touch()"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-7">
              <form-item
                :with-slotted-error="$v.registryAddressStreetName.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressStreetName"
                  title="Название улицы"
                  :error="$v.registryAddressStreetName.$error"
                  :valid="!$v.registryAddressStreetName.$invalid"
                  @blur="$v.registryAddressStreetName.$touch()"
                />
              </form-item>
            </div>
          </div>

          <div class="row">
            <div class="col-md-12 col-lg-4">
              <form-item
                :with-slotted-error="$v.registryAddressHouse.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressHouse"
                  title="Дом"
                  :error="$v.registryAddressHouse.$error"
                  :valid="!$v.registryAddressHouse.$invalid"
                  @blur="$v.registryAddressHouse.$touch()"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-4">
              <form-item>
                <pos-input
                  v-model.trim="registryAddressDoorway"
                  title="Корпус/Строение"
                  :valid="!$v.registryAddressDoorway.$invalid"
                />
              </form-item>
            </div>
            <div class="col-md-12 col-lg-4">
              <form-item
                :with-slotted-error="$v.registryAddressApartment.$error"
                error="Заполните поле"
              >
                <pos-input
                  v-model.trim="registryAddressApartment"
                  title="Квартира"
                  :mask="$$mask.onlyNumberMask"
                  :error="$v.registryAddressApartment.$error"
                  :valid="!$v.registryAddressApartment.$invalid"
                  @blur="$v.registryAddressApartment.$touch()"
                />
              </form-item>
            </div>
          </div>
        </template>
        <template #main-button>
          <pos-button
            :disabled="$v.manualAddressGroup.$error"
            @click="saveRegistryAddressFields"
          >
            Сохранить
          </pos-button>
        </template>
        <template #secondary-button>
          <div @click="resetRegistryAddressFields">
            Сбросить
          </div>
        </template>
      </address-modal>
    </portal>
  </div>
</template>

<script>
import { mapFields } from 'vuex-map-fields';
import { passportData } from '@/utils/validation';
import { createAddressString } from '@/utils/string';

import {
  REGION_REFERENCE,
  SETTLEMENT_TYPE,
  STREET_TYPE,
} from '@/utils/references';

import FormWrapper from '@/components/FormWrapper';
import FormItem from '@/components/FormItem';
import PosInput from '@/components/Input';
import PosCheckbox from '@/components/Checkbox';
import PosButton from '@/components/Button';
import PosDropdown from '@/components/Dropdown';
import Suggestions from '@/components/Suggestions';
import AddressModal from '@/components/AddressModal';
import { mapActions } from 'vuex';

export default {
  name: 'PassportData',
  components: {
    FormWrapper,
    FormItem,
    PosInput,
    PosCheckbox,
    PosButton,
    Suggestions,
    AddressModal,
    PosDropdown,
  },
  validations: passportData,
  data() {
    return {
      isShowAddressModal: false,
      regionReference: REGION_REFERENCE,
      settlementType: SETTLEMENT_TYPE,
      streetType: STREET_TYPE,
    };
  },
  computed: {
    ...mapFields({
      issuerCode: 'form.issuerCode',
      issuerName: 'form.issuerName',
      placeOfBirth: 'form.placeOfBirth',
      registryAddressKladrCode: 'form.registryAddressKladrCode',
      factAddressKladrCode: 'form.factAddressKladrCode',
      showStepperModal: 'modals.showStepperModal',
      factRegistryAddressFlg: 'form.factRegistryAddressFlg',
      registryAddress: 'form.registryAddress',
      factAddress: 'form.factAddress',

      registryAddressZipcode: 'form.registryAddressZipcode',
      registryAddressRegion: 'form.registryAddressRegion',
      registryAddressDistrict: 'form.registryAddressDistrict',
      registryAddressHumanSettlement: 'form.registryAddressHumanSettlement',
      registryAddressSettlementName: 'form.registryAddressSettlementName',
      registryAddressTown: 'form.registryAddressTown',
      registryAddressStreet: 'form.registryAddressStreet',
      registryAddressStreetName: 'form.registryAddressStreetName',
      registryAddressHouse: 'form.registryAddressHouse',
      registryAddressDoorway: 'form.registryAddressDoorway',
      registryAddressApartment: 'form.registryAddressApartment',

      manualAddress: 'manualAddress',
      isLoading: 'isLoading',
      completeAddress: 'completeAddress',
    }),
  },
  watch: {
    factRegistryAddressFlg(val) {
      if (val && this.factAddress) {
        this.factAddress = '';
        this.factAddressKladrCode = '';
        this.$v.factAddress.$reset();
      }
    },
  },
  methods: {
    ...mapActions(['sendShortLoan']),

    click() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.factRegistryAddressFlg) {
        this.factAddressKladrCode = this.registryAddressKladrCode;
      }
      this.showStepperModal = false;
      this.$router.push('contacts');
    },
    setIssuerName(suggestion) {
      const issuerName = suggestion.value;
      if (issuerName) {
        this.issuerName = issuerName;
      }
    },
    getKladrId(suggestion) {
      const { data } = suggestion;
      return data ? data.kladr_id : '';
    },
    onAddress(suggestion, field) {
      this[field] = this.getKladrId(suggestion);
      const { data } = suggestion;
      this.completeAddress = !!data.house;
    },
    onAddressBlur(suggestion, field, touch) {
      if (!this[field]) {
        this[field] = this.getKladrId(suggestion);
      }
      this.$v[touch].$touch();
    },
    showSuggestionsList() {
      this.completeAddress = false;
      this.manualAddress = false;
    },
    resetRegistryAddressFields() {
      this.registryAddressZipcode = '';
      this.registryAddressRegion = '';
      this.registryAddressTown = '';
      this.registryAddressStreet = '';
      this.registryAddressStreetName = '';
      this.registryAddressHouse = '';
      this.registryAddressDoorway = '';
      this.registryAddressApartment = '';
      this.registryAddressDistrict = '';
      this.registryAddressHumanSettlement = '';
      this.registryAddressSettlementName = '';

      this.closeAndResetAddressModal();
    },
    saveRegistryAddressFields() {
      this.$v.manualAddressGroup.$touch();
      if (this.$v.manualAddressGroup.$invalid) {
        return;
      }
      this.registryAddress = createAddressString(
        this.registryAddressTown,
        this.registryAddressHumanSettlement,
        this.registryAddressSettlementName,
        this.registryAddressStreet,
        this.registryAddressStreetName,
        this.registryAddressHouse,
        this.registryAddressDoorway,
        this.registryAddressApartment,
      );
      this.completeAddress = true;
      this.isShowAddressModal = false;
    },
    showAddressModal() {
      this.resetRegistryAddressFields();
      this.isShowAddressModal = true;
      this.manualAddress = true;
    },
    closeAndResetAddressModal() {
      this.isShowAddressModal = false;
      this.manualAddress = false;
      this.$v.manualAddressGroup.$reset();
    },
  },
};
</script>

<style lang="scss" scoped>
  .passport-data {
    &__check-text {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
    }
  }
</style>
