<template>
  <div
    v-click-outside="close"
    class="pos-dropdown"
  >
    <div
      class="pos-dropdown__activator"
      @click="visibleControl"
    >
      <pos-input
        :title="title"
        :value="selected"
        :error="error"
        for-dropdown
        readonly
      >
        <template v-slot:suffix>
          <chevron-bottom v-if="!show" />
          <chevron-top v-else />
        </template>
      </pos-input>
    </div>
    <div
      v-if="items.length && show"
      class="pos-dropdown__list"
    >
      <div
        v-for="(item, index) in items"
        :key="index"
        class="pos-dropdown__item"
        :class="{ '_selected': item.value === selectedValue.value }"
        @click="select(item)"
      >
        {{ item.title }}
      </div>
    </div>
  </div>
</template>

<script>
import PosInput from '@/components/Input';
import { ChevronBottom, ChevronTop } from '@/components/icons';

const defaultData = {
  title: '',
  value: '',
};

export default {
  name: 'Dropdown',
  components: {
    PosInput,
    ChevronBottom,
    ChevronTop,
  },
  model: {
    prop: 'value',
    event: 'input',
  },
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    items: {
      type: Array,
      default: () => [],
    },
    error: Boolean,
  },
  data() {
    return {
      selected: '',
      show: false,
    };
  },
  computed: {
    selectedValue() {
      const selected = this.items.find(item => item.value === this.value);
      return selected || '';
    },
  },
  watch: {
    selectedValue: {
      handler() {
        this.updateValue(this.selectedValue || defaultData);
      },
      immediate: true,
    },
  },
  methods: {
    select(data) {
      this.show = false;
      this.updateValue(data);
    },
    visibleControl() {
      this.show = !this.show;
    },
    close() {
      this.show = false;
    },
    updateValue(data) {
      this.selected = data.title;
      this.$emit('input', data.value);
    },
  },
};
</script>

<style lang="scss">
  .pos-dropdown {
    position: relative;

    &__activator {
      cursor: pointer;
    }

    &__list {
      position: absolute;
      z-index: 1;
      left: 0;
      top: calc(100% + 8px);
      max-height: 226px;
      width: 100%;
      border: 1px solid $gray_5;
      border-radius: 4px;
      background-color: $white;
      box-shadow: 0 4px 16px rgba(0, 0, 0, .1);
      overflow-y: auto;
    }

    &__item {
      font-size: 16px;
      line-height: 22px;
      padding: 17px 24px;
      cursor: pointer;
      color: $dark;

      &._selected {
        font-weight: 500;
      }

      &:hover {
        background-color: $gray_7;
      }
    }
  }
</style>
