<template>
  <div class="pos-stepper">
    <div class="pos-stepper__content">
      <stepper-step
        v-for="(module, index) in modules"
        :key="index"
        :active="module.id <= currentRouteId"
        :completed="module.id < currentRouteId"
        :show-line="showLine(module.id)"
      />
    </div>
  </div>
</template>

<script>
import { ROUTE_IDS } from '@/utils/constants';
import { MODULES_FORM_SHORT, MODULES_FORM_FULL } from './modules';

import StepperStep from './StepperStep';

export default {
  name: 'Stepper',
  components: { StepperStep },
  computed: {
    showStepperModal() {
      return this.$store.state.modals.showStepperModal;
    },
    currentRouteId() {
      return this.$route.meta.id;
    },
    modules() {
      return this.$store.state.formType === 'full'
        ? MODULES_FORM_FULL
        : MODULES_FORM_SHORT;
    },
  },
  methods: {
    showLine(stepId) {
      return (stepId === ROUTE_IDS.CONTACT_INFO)
        && (this.currentRouteId > ROUTE_IDS.CONTACT_INFO) && this.showStepperModal;
    },
  },
};
</script>

<style lang="scss">
  .pos-stepper {
    position: relative;
    width: 100%;
    max-width: 256px;
    padding: 10px 0;
    user-select: none;

    &__content {
      display: flex;
    }
  }
</style>
