<template>
  <component
    :is="tag"
    class="online-btn"
    :class="classes"
    :disabled="disabled"
    @click="$emit('click')"
  >
    <span class="btn-text">
      <slot />
    </span>
    <div class="online-loader">
      <loader />
    </div>
  </component>
</template>

<script>
import Loader from './Loader';

const TAGS = ['button', 'a', 'router-link'];

export default {
  name: 'Button',
  components: { Loader },
  props: {
    tag: {
      type: String,
      default: TAGS[0],
      validate: type => TAGS.includes(type),
    },
    kind: {
      type: String,
      default: 'default',
      validator: value => ['default', 'outline'].includes(value),
    },
    disabled: Boolean,
    isLoading: Boolean,
  },
  computed: {
    classes() {
      return {
        [`_${this.kind}`]: this.kind,
        _loading: this.isLoading,
        _disabled: this.disabled,
      };
    },
  },
};
</script>

<style lang="scss">

  .online-btn {
    display: inline-block;
    font-size: $main-text;
    font-weight: 500;
    border-radius: $btn-border-radius;
    box-shadow: 0 4px 24px rgba(157, 157, 157, .15);
    cursor: pointer;
    text-align: center;
    transition: .2s linear;
    border: 1px solid transparent;
    line-height: $line-height;
    padding: 20px 30px;
    width: 100%;

    @include media-breakpoint-up(lg) {
      width: 317px;
    }

    &._default {
      background: $green_1;
      color: $white;
    }

    &._outline {
      background: $white;
      color: $green_1;
      border-color: $green_1;
      border-width: 2px;
    }

    &:hover {
      background: $green_2;
      color: $white;
    }

    &._disabled {
      background: $green_5;
      color: $white;
      cursor: default;
      outline: none;
      box-shadow: none;

      &:hover {
        background: $green_5;
        color: $white;
        box-shadow: none;
      }

      &:focus {
        outline: none;
      }
    }

    &._loading {
      .btn-text {
        display: none;
      }

      .online-loader {
        display: block;
      }

      .dotted-loader {
        svg {
          width: 70px;
          height: 16px;
        }
      }
    }

    .online-loader {
      display: none;
    }

    &:focus {
      outline: $gray_2 auto 1px;
    }
  }
</style>
