import store from '@/store';

const DATE_LENGTH = 10;
const PHONE_LENGTH = 15;

const dateOfBirthText = (error, value) => {
  if (!error && value.length === DATE_LENGTH) {
    return 'Минимальный допустимый возраст от 18 до 69 лет';
  }
  return 'Укажите дату рождения';
};

const fmsDateText = (error, value) => {
  if (
    !error
    && (store.state.form.dateOfBirth.length !== DATE_LENGTH
    || !store.state.form.dateOfBirth)) {
    return 'Введите дату рождения';
  }
  if (!error && value.length === DATE_LENGTH) {
    return 'Срок действия вашего паспорта истёк';
  }
  return 'Укажите дату выдачи паспорта';
};


const registryAddressDateText = (error, value) => {
  if (!error && value.length === DATE_LENGTH) {
    return 'Введите корректное значение';
  }
  return 'Укажите дату регистрации';
};

const phoneNumberText = (value) => {
  if (
    value.length !== PHONE_LENGTH
    && value.length > 0
  ) {
    return 'Номер телефона должен состоять из 10 цифр';
  }
  return 'Укажите номер телефона';
};

const fioText = (value) => {
  if (value.includes(' ')) {
    return 'Укажите имя';
  }
  return 'Укажите ваши фамилию, имя и отчество через пробел';
};

const anotherPartnerFio = (value) => {
  if (value.includes(' ')) {
    return 'Укажите имя и отчество';
  }
  return 'Для контактного лица достаточно имени и фамилии';
};

const partnerFio = (value) => {
  if (value.includes(' ')) {
    return 'Укажите имя и отчество';
  }
  return 'Как обращаться к супруге/ супругу';
};

const addressText = (error, value) => {
  if (!value) {
    return 'Укажите адрес регистрации';
  }
  if (
    (value.includes('д ') || value.includes('стр '))
    && error
  ) {
    return 'Выберите адрес из списка';
  }
  return 'Укажите адрес полностью, включая номер дома';
};

export default {
  dateOfBirthText,
  phoneNumberText,
  fmsDateText,
  registryAddressDateText,
  fioText,
  anotherPartnerFio,
  partnerFio,
  addressText,
};
