<template>
  <div
    class="pos-modal__backdrop"
    role="dialog"
  >
    <div
      class="pos-modal__dialog"
      role="document"
      :class="classes"
    >
      <div class="pos-modal__content">
        <header
          class="pos-modal__header"
        >
          <img
            class="pos-modal__close"
            src="./modal-close.svg"
            alt="modal-close"
            width="24"
            height="24"
            @click="close"
          >
        </header>
        <section
          class="pos-modal__body"
        >
          <slot />
        </section>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ModalWindow',
  props: {
    size: {
      type: String,
      default: 'size-xl',
      validator: value => ['size-xl', 'size-md', 'size-sm'].includes(value),
    },
  },
  computed: {
    classes() {
      return {
        [`_${this.size}`]: this.size,
      };
    },
  },
  methods: {
    close() {
      this.$emit('close');
    },
  },
};
</script>

