import {
  required, sameAs, requiredUnless, requiredIf,
} from 'vuelidate/lib/validators';
import { regex } from '@/utils/regex';
import { getFullAge, formatDate } from '@/utils/date';
import { SOCIAL_STATUSES } from './constants';
import store from '@/store';

/**
 * Проверить заполнено ли передаваемое поле
 * @param {string} value,
 * @returns {boolean}
 */
const isValidInn = (value = '') => {
  const LEGAL_ENTITY_INN = 12;
  const INDIVIDUAL_INN = 10;

  if (
    value
    && (value.length !== LEGAL_ENTITY_INN)
    && (value.length !== INDIVIDUAL_INN)
  ) {
    return false;
  }
  return !value.includes('_');
};

/* Проверяет корректное заполнение полей ввода мобильного номера телефона */
const isValidPhone = (value = '') => {
  const PHONE_LENGTH = 15;
  if (!value) {
    return true;
  } return value.length >= PHONE_LENGTH;
};

const isValidField = fieldName => (value = '') => {
  if (!value) {
    return true;
  } return regex[fieldName].test(value);
};

const isValidPassportInfo = (value = '') => {
  const PASSPORT_INFO_LENGTH = 11;
  return value.length >= PASSPORT_INFO_LENGTH;
};

const isValidIssuerCode = (value = '') => {
  const ISSUER_CODE_LENGTH = 7;
  return value.length >= ISSUER_CODE_LENGTH;
};

const isValidAddress = () => !!store.state.completeAddress || store.state.manualAddress;

/* Проверяет корректное заполнение поля 'Дополнительный телефон для связи' */
const isValidFactLivingPhone = (factLivingPhone = '') => {
  const MOBILE_PHOHE_LENGTH = 17;
  const STATIONARY_PHOHE_LENGTH = 16;
  if (
    factLivingPhone
    && factLivingPhone.includes('+')) {
    return factLivingPhone.length === MOBILE_PHOHE_LENGTH;
  } if (factLivingPhone) {
    return factLivingPhone.length === STATIONARY_PHOHE_LENGTH;
  } return true;
};

/* Проверяет корректное заполнение дохода */
const isValidIncome = (value = '') => value[0] !== '0';

/* Проверка смс кода */
const CURRENT_CODE = '1234';

const isValidSmsCode = (code = '') => code === CURRENT_CODE;

/**
 * Проверяет находится ли возраст в промежутке от 18 до 100 лет,
 * а также корректно ли введена дата выдачи паспорта и дата регистрации по месту прописки
 * @param {string} fieldName  - 'dateOfBirth',
 * @returns {boolean} - true
 */
const isValidDate = fieldName => (dateString = '') => {
  if (!dateString) return true;

  const DATE_LENGTH = 10;
  const MIN_AGE = 18;
  const MAX_AGE = 100;
  const MIN_FMS_DATE = 14;
  const MAX_FMS_DATE = 45;
  const today = new Date();
  const formattedDate = new Date(formatDate(dateString));
  const field = name => store.state.form[name];
  const dateOfBirthDate = new Date(formatDate(field('dateOfBirth')));

  if (dateString.length !== DATE_LENGTH) return false;

  if (fieldName === 'dateOfBirth') {
    if (getFullAge(dateString) <= MIN_AGE || getFullAge(dateString) >= MAX_AGE) return false;
  }

  /* Проверка корректного значения поля "дата выдачи пасспорта" */
  if (fieldName === 'fmsDate') {
    const isBeforeFourteenYears = getFullAge(field('dateOfBirth')) - getFullAge(dateString) < MIN_FMS_DATE;
    const isEmptyField = field('dateOfBirth').length !== DATE_LENGTH;
    const isInvalidDate = Number.isNaN(getFullAge(field('dateOfBirth')));
    const isOlderFortyFiveYears = getFullAge(field('dateOfBirth')) >= MAX_FMS_DATE;
    const isExpiredPassport = +formattedDate
      <= dateOfBirthDate.setFullYear(dateOfBirthDate.getFullYear()
        + MAX_FMS_DATE);

    if (isInvalidDate
      || isEmptyField
      || formattedDate > today
      || getFullAge(field('dateOfBirth')) <= MIN_AGE
      || getFullAge(field('dateOfBirth')) >= MAX_AGE
      || isBeforeFourteenYears
      || (isOlderFortyFiveYears
      && isExpiredPassport)
    ) {
      return false;
    }
  }
  if (fieldName === 'registryAddressDate') {
    if (
      formattedDate < new Date(formatDate(field('dateOfBirth')))
      || formattedDate > today
    ) {
      return false;
    }
  }

  const MONTH_LENGTH = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  const [day, month, year] = dateString.split('.');

  /* Проверка на високосный год */
  if (year % 400 === 0 || (year % 100 !== 0 && year % 4 === 0)) MONTH_LENGTH[1] = 29;
  return day > 0 && day <= MONTH_LENGTH[month - 1];
};


/* Валидируемые поля формы для каждой страницы */
const personalData = () => ({
  clientName: {
    required,
    isValidField: isValidField('fio'),
  },
  sex: {
    required,
  },
  dateOfBirth: {
    required,
    isValidDate: isValidDate('dateOfBirth'),
  },
  passportInfo: {
    required,
    isValidPassportInfo,
  },
  fmsDate: {
    required,
    isValidDate: isValidDate('fmsDate'),
  },
  monthIncome: {
    required,
    isValidIncome,
  },
  agree: {
    required,
    sameAs: sameAs(() => true),
  },
});

const contactInfo = () => ({
  phoneContacts: {
    required,
    isValidPhone,
  },

  clientEmail: {
    required,
    isValidField: isValidField('email'),
  },

  codeWord: {
    required,
    isValidField: isValidField('codeWord'),
  },
});

const passportData = () => ({
  issuerCode: {
    required,
    isValidIssuerCode,
  },
  issuerName: {
    required,
    isValidField: isValidField('issuerName'),
  },
  placeOfBirth: {
    required,
    isValidField: isValidField('placeOfBirth'),
  },
  registryAddress: {
    required,
    isValidAddress,
  },
  factAddress: {
    required: requiredUnless('factRegistryAddressFlg'),
    isValidField: isValidField('address'),
  },
  registryAddressZipcode: {},
  registryAddressRegion: {
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressDistrict: {
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressHumanSettlement: {
    required: requiredIf(function validator() {
      return this.registryAddressSettlementName !== '' && this.registryAddressTown === '';
    }),
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressSettlementName: {
    required: requiredIf(function validator() {
      return this.registryAddressHumanSettlement !== '' && this.registryAddressTown === '';
    }),
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressTown: {
    required: requiredIf(function validator() {
      return this.registryAddressSettlementName === ''
        && this.registryAddressHumanSettlement === ''
        && this.manualAddress;
    }),
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressStreet: {
    required: requiredIf(function validator() {
      return this.registryAddressStreetName !== '';
    }),
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressStreetName: {
    required: requiredIf(function validator() {
      return this.registryAddressStreet !== '';
    }),
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressHouse: {
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressDoorway: {
    isValidField: isValidField('withoutLatinLetters'),
  },
  registryAddressApartment: {
    isValidField: isValidField('withoutLatinLetters'),
  },
  manualAddressGroup: [
    'registryAddressZipcode',
    'registryAddressRegion',
    'registryAddressDistrict',
    'registryAddressHumanSettlement',
    'registryAddressSettlementName',
    'registryAddressTown',
    'registryAddressStreet',
    'registryAddressStreetName',
    'registryAddressHouse',
    'registryAddressDoorway',
    'registryAddressApartment',
  ],
});

const workInfo = () => ({
  socialStatus: {
    required,
  },
  organizationName: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || (!this.pension && !this.isExactlyPensioner);
    }),
  },
  INN: {
    isValidInn,
  },
  periodOfWork: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || this.socialStatus === SOCIAL_STATUSES.WORKS;
    }),
  },
  industry: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || this.socialStatus === SOCIAL_STATUSES.WORKS;
    }),
  },
  workAddress: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || this.socialStatus === SOCIAL_STATUSES.WORKS;
    }),
    isValidField: isValidField('address'),
  },
  position: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || this.socialStatus === SOCIAL_STATUSES.WORKS;
    }),
  },
  phoneWork: {
    required: requiredIf(function validator() {
      return this.showEmployedFields || this.socialStatus === SOCIAL_STATUSES.WORKS;
    }),
    isValidPhone,
  },
  factLivingPhone: {
    required: requiredIf(function validator() {
      return this.showNoEmployedFields;
    }),
    isValidFactLivingPhone,

  },
});

const additionalInfo = () => ({
  education: {
    required,
  },
  maritalStatus: {
    required,
  },
  partnerFIO: {
    required: requiredIf(function validator() {
      return this.showFio;
    }),
    isValidField: isValidField('fio'),
  },
  numberOfChildren: {
    required,
  },
  familyIncome: {
    required,
    isValidIncome,
  },
  registryAddressDate: {
    required,
    isValidDate: isValidDate('registryAddressDate'),
  },
  factAddressPeriod: {
    required,
  },
  additionalPhoneOwner: {},
  phoneAdditional: {
    isValidPhone,
  },
  anotherPersonFIO: {
    isValidField: isValidField('fio'),
  },
});

const smsPage = () => ({
  code: {
    required,
    isValidSmsCode,
  },
});

export {
  contactInfo,
  personalData,
  passportData,
  workInfo,
  additionalInfo,
  smsPage,
};
