// Формируем поле 'комментарий' для отправки на сервер
export const addComment = state => clientData.reduce((acc, field) => {
  const [key, value] = field.data.split('.');
  // Пожелание для отображения комментария в ecom
  let fieldValue = state[key][value];
  if (fieldValue === true) {
    fieldValue = 'Да';
  } else if (fieldValue === false) {
    fieldValue = 'Нет';
  }
  return `${acc + field.label} ${fieldValue} <br>`;
}, '');
