<template>
  <section class="contact-info">
    <form-wrapper>
      <template v-slot:info>
        Пожалуйста, представьтесь. Чем больше вы расскажете о себе, тем выше шансы на <br>
        получение кредита. После отправки заявки вам позвонит сотрудник Банка.
      </template>

      <template v-slot:title>
        Контактная информация
      </template>

      <template v-slot:default>
        <div class="row">
          <div class="col-lg-5 col-md-12">
            <form-item
              description="Отправим СМС с кодом подтверждения"
              :with-slotted-error="$v.phoneContacts.$error"
              :error="$$error.phoneNumberText(phoneContacts)"
            >
              <pos-input
                v-model="phoneContacts"
                :pattern="$$pattern.phonePattern"
                autocomplete="off"
                type="tel"
                placeholder="(921) 123-45-67"
                prefix="+7"
                inputmode="tel"
                :mask="$$mask.phone"
                :error="$v.phoneContacts.$error"
                :valid="!$v.phoneContacts.$invalid"
                @blur="$v.phoneContacts.$touch()"
              >
                Мобильный телефон
              </pos-input>
            </form-item>
          </div>
          <div class="col-lg-7 col-md-12">
            <form-item
              description="Пришлем письмо с информацией по вашей заявке"
              :with-slotted-error="$v.clientEmail.$error"
              error="Введите корректный адрес эл. почты."
            >
              <pos-input
                v-model="clientEmail"
                :mask="$$mask.emailMask"
                placeholder="ivan@domain.ru"
                :error="$v.clientEmail.$error"
                :valid="!$v.clientEmail.$invalid"
                @blur="$v.clientEmail.$touch()"
              >
                Электронная почта
              </pos-input>
            </form-item>
          </div>
        </div>

        <form-item
          description="Например, девичья фамилия вашей матери"
          :with-slotted-error="$v.codeWord.$error"
          error="Далее потребуется для доступа к управлению услугами"
        >
          <pos-input
            v-model="codeWord"
            translate
            :mask="$$mask.codeWordMask"
            :error="$v.codeWord.$error"
            :valid="!$v.codeWord.$invalid"
            @blur="$v.codeWord.$touch()"
          >
            Придумайте кодовое слово
          </pos-input>
        </form-item>
      </template>

      <template v-slot:buttons>
        <pos-button
          :disabled="$v.$error"
          :is-loading="isLoading"
          @click="click"
        >
          Далее
        </pos-button>
      </template>
    </form-wrapper>
  </section>
</template>

<script>

import { mapActions } from 'vuex';
import { mapFields } from 'vuex-map-fields';
import { contactInfo } from '@/utils/validation';

import FormWrapper from '@/components/FormWrapper';
import PosInput from '@/components/Input';
import PosButton from '@/components/Button';
import FormItem from '@/components/FormItem';

export default {
  name: 'ContactInfo',
  components: {
    FormWrapper,
    PosInput,
    PosButton,
    FormItem,
  },
  validations: contactInfo,
  computed: {
    ...mapFields({
      clientEmail: 'form.clientEmail',
      codeWord: 'form.codeWord',
      scoring: 'form.scoring',
      phoneContacts: 'form.phoneContacts',
      isLoading: 'isLoading',
    }),
  },
  watch: {
    phoneContacts(val) {
      if (val) {
        this.$store.commit('setFullMobilePhone', { name: 'clientMobilePhone', value: val });
      }
    },
  },
  methods: {
    ...mapActions(['sendShortLoan']),

    click() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.isLoading = true;
      this.sendShortLoan().then(() => {
        this.isLoading = false;
        this.$router.push('success');
      });
    },
  },
};
</script>

